import React from 'react';
import { connect } from 'react-redux';
import { confirmAlert } from 'react-confirm-alert';
import { Redirect } from 'react-router-dom';

import { deleteProfile } from '../../store/profiles';

const GalleryCard = ({data, _id,deleteProfile}) => {
    const del = async () => {
        return confirmAlert({
            title: 'Confirm to delete',
            message: 'Are you sure you want to delete this image',
            buttons: [
                {
                    label: 'Yes',
                    onClick: () => {
                        deleteProfile(_id, data);
                        return;
                    }
                },
                {
                    label: 'No',
                    onClick: () => {
                        return <Redirect to={`/admin/hostels`} />;
                    }
                }
            ]
        })
    }

    return (
        <div className="card gallery_card">
            <img src={data} className="card-img-top gallery_card_img" alt={`${data}`} />
            <div className="card-body">
                <div className="media">
                    <div className="media-body">
                        <button className="btn btn-sm btn-outline-danger" onClick={
                            () => del()
                        }>delete</button>
                    </div>
                </div>
            </div>
        </div>
    )
};

const dispatchStateToProps = {
    deleteProfile
}

export default connect(null, dispatchStateToProps)(GalleryCard);