import {ADMIN_PENDING,ADMIN_SUCCESS} from './types';
const initialState = {
    pending:false,
    admin:{}
};
export function adminReducer(state=initialState,action){
    switch(action.type){
        case ADMIN_PENDING:
            return {
                ...state,
                pending:true
            };

        case ADMIN_SUCCESS:
            return {
                ...state,
                pending:false,
                admin:action.payload
            };
        
        default:
            return {
                ...state
            }
    }
};

export const getAdminData = state => state.admin;