import React,{useState,useEffect} from 'react';
import {BeatLoader} from 'react-spinners';
import {Link} from 'react-router-dom';
import {connect} from 'react-redux';

// import {fetchHostels} from '../../utils/data';
import Banner from '../../shared/Banner';
import Carousel from '../../shared/Carousel';
import HostelsCard from '../../shared/HostelCard';
import {fetchHostels,getHostelsData} from '../../../store/hostels';

const Hostels = (props) => {
     
const [loading,setLoading] = useState(true);
const [hostels,setHostels] = useState([]);
const {received,fetchHostels,pending} = props;

useEffect(() => {
    const load_hostels =  () => {
        fetchHostels();
        if(!pending){
            setHostels(received);
            return setLoading(false);
        }      
    };
    load_hostels();
},[pending]);

    if(loading) return (
        <div className="centered_loader_component_holder">
            <div className="centered_component_loader">
                <BeatLoader margin="2" size="15" color="#009933" />
            </div>
        </div>
    );

    if(hostels.length === 0) return (
        <div className="centered_loader_component_holder">
            <div className="centered_component_loader">
                <Banner message={`There are no hostels added yet`} />
                <Link to={`/admin/hostel_form`} className="btn btn-outline-success">upload hostel</Link>
            </div>
        </div>
    );

     return (
         <section className="container">
            {
                hostels.length >= 3 && <Carousel card="hostels" title="Uploaded hostels" data={hostels} see_more={true} />
            }
            {
                hostels.length < 3 && <div className="row">
                    {
                        hostels.map((hostel,index) => (
                            <div className="col-12 col-sm-4 col-md-4" key={index}>
                                   <HostelsCard data={hostel} key={index} />
                            </div>
                        ))
                    }
                </div>
            }
            <div className="row" style={{marginTop:"15px"}}>
                <div className="col-12 col-sm-12 col-md-12 col-lg-12">
                    <Link to={`/admin/hostel_form`} className="btn btn-sm btn-outline-success">add hostel</Link>
                </div>
            </div>
         </section>
     )
};

const mapStateToProps = state => ({
    pending:getHostelsData(state).pending,
    received:state.hostels.hostels
});
const dispatchStateToProps = {
    fetchHostels
}

export default connect(mapStateToProps, dispatchStateToProps)(Hostels);