import React from 'react';
//core components

const NewsCard = ({data:post}) => {
    return (
        <div className="card news_card">
            <img src={`${post.urlToImage}`} className="card-img-top news_card_img" alt={post.title} />
            <div className="card-body">
            <span className="badge badge-danger">{post.source.name}</span>
            <a className="card_heading" href={post.url}>{post.title}</a>
            </div>
        </div>
    )
};

export default NewsCard;