import {PRODUCTS_PENDING,PRODUCTS_SUCCESS} from './types';
const initialState = {
    pending:false,
    products:[]
};

export function productsReducer(state=initialState,action){
    switch(action.type){
        case PRODUCTS_PENDING:
            return {
                ...state,
                pending:true
            };

        case PRODUCTS_SUCCESS:
            return {
                ...state,
                pending:false,
                products:action.payload
            };

        default:
            return {...state}
    }
};

export const getProductsData  = state => state.products;