import {HOSTEL_DELETE,HOSTEL_PENDING,HOSTEL_SUCCESS} from './types';
const initialState = {
    pending:true,
    hostels:[]
};

export const reducer = (state=initialState,action) => {
    switch(action.type){
        case HOSTEL_PENDING:
            return {
                ...state,
                pending:true
            }
        case HOSTEL_DELETE:
            return {
                ...state,
                pending:false,
                hostels:state.hostels.filter(hostel => hostel._id !== action.payload)
            }
        case HOSTEL_SUCCESS:
            return {
                ...state,
                pending:false,
                hostels:action.payload
            }
        default:
            return {
                ...state
            }
    }
};

export const getHostelsData = state => state.hostels;