import React,{useState,useEffect} from 'react';
import {ScaleLoader} from 'react-spinners';


//core-components
import View from './View';
import Pagination from "./Pagination";
import {loadNotifs,deleteNotif} from '../../utils/data';

const Notifications = () => {

    const [loading,setLoading] = useState(true);
    const [notifs,setNotifs] = useState([]);
    const [token] = useState(localStorage.getItem('admin_token'));
    const [currentPage,setCurrentPage] = useState(1);
    const [notifsPerPage] = useState(5);

    useEffect(() => {
        const loadNotifications = async () => {            
            let _notifs = await loadNotifs(token).catch(console.log);
            setNotifs(_notifs);
            return setLoading(false);            
        };
        loadNotifications();
    },[token]);

    const indexOfLastNotif = currentPage * notifsPerPage;
    const indexOfFirstNotif = indexOfLastNotif - notifsPerPage;
    const currentNotifs = notifs.slice(indexOfFirstNotif,indexOfLastNotif);
    
    const deleteNotification = async _id => {
        setLoading(true);
        let {success,message} = await deleteNotif(_id).catch(console.log);
        if(success) {
            setNotifs(message);
            return setLoading(false);
        }
    };
    const paginate = number => setCurrentPage(number);

    if(loading) return (
        <div className="centered_loader_component_holder">
            <div className="centered_component_loader">
                <ScaleLoader height="35" width="4" radius="2" margin="2px" color="#009933" />
            </div>
        </div>
    );

    return (
        <section>
            <div className="row notifications">
                <div className="col-12 col-sm-12">
                    <View notifs={currentNotifs} deleteNotif={deleteNotification}/>
                    <Pagination notifsPerPage={notifsPerPage} current={currentPage} paginate={paginate} totalNotifs={notifs.length}/>
                </div>
            </div>
        </section>
    );


};


export default Notifications;