import React from 'react';
import {Link} from 'react-router-dom';
//core-components
import avatar from '../../assets/img/faces/marc.jpg';

const EventCard = ({data:event}) => {
    return (
        <div className="card events_card">
            <img src={`${event.profiles[0]}`} className="card-img-top event_card_img" alt={event.name} />
            <div className="card-body">
                <div className="media">
                    <img className="mr-3 event_card_organizer--profile" alt={event.organizer.firstname} src={
                        event.organizer.profile ? event.organizer.profile : avatar
                    } />
                    <div className="media-body">
                        <Link className="card_heading" to={`/admin/event/${event._id}`}>{event.name}</Link>
                        <p className="media-text card_text">By {event.organizer.firstname}</p>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default EventCard;