import React from 'react';
import classNames from 'classnames';

const Pagination = ({notifsPerPage,totalNotifs,paginate,current}) => {

    let pageNumbers = [];
    for(let i=1;i<=Math.ceil(totalNotifs/notifsPerPage);i++){
        pageNumbers.push(i);
    };

    return (
        <nav>
            <ul className="pagination">
                {
                    pageNumbers.map(number => {
                        let page_class = classNames({
                            'page-item': true,
                            'page-item active': current === number
                        });
                        return (
                            <li key={number} className={page_class}>
                                <span onClick={() => paginate(number)} className="page-link">
                                    {number}
                                </span>
                            </li>
                        )
                    })
                }
            </ul>
        </nav>
    )
};


export default Pagination;