import axios from 'axios';

import {profilePending,profileSuccess,profileDelete,profileAdd} from './actions';
const URL = `https://karuapi.herokuapp.com/api/`;

export const fetchProfiles = (_id) => {
    return dispatch => {
        //fetching a specific profile
        dispatch(profilePending());
        axios.get(`${URL}/hostels/${_id}`).then(result => {
            let {profiles} = result.data.message;
            return dispatch(profileSuccess(profiles));
        }).catch(console.log);        
    }
};

export const addProfile = (_id,data) => {
    return dispatch => {
        dispatch(profilePending());
        axios.post(`${URL}/hostels/updateProfile/${_id}`,data,{
            headers:{'Content-Type':'multipart/form-data'}
        }).then(result => {
            let profile = result.data.message;
            return dispatch(profileAdd(profile));
        }).catch(console.log);
    }
};

export const deleteProfile = (_id,data) => {
    return dispatch => {
        dispatch(profilePending());
        axios.post(`${URL}/hostels/deleteProfile/${_id}`,{
            data
        },{            
            headers:{'Content-Type':'application/json'}            
        }).then(res => {
            console.log('the data sent back',res.data.message);
            return dispatch(profileDelete(res.data.message))
        }).catch(console.log);
    }
};