//what are we supposed to do 
//fetch hostels
//handle delete of hostels
//the actions are pretty simple the fetch of hostels and delete of hostels
import {HOSTEL_PENDING,HOSTEL_SUCCESS,HOSTEL_DELETE} from './types';

export const hostelPending = () => {
    return {
        type:HOSTEL_PENDING
    }
};

export const hostelSuccess = (data) => {
    return {
        type:HOSTEL_SUCCESS,
        payload:data
    }
};

export const deleteHostelSuccess = (data) => {
    return {
        type:HOSTEL_DELETE,
        payload:data
    }
};