import React from 'react';
import PropTypes from 'prop-types';
import {Link} from 'react-router-dom';
import {FaAngleRight} from 'react-icons/fa';

/** should we have a read more */

const BlockHeader = ({ title,link }) => {
    return (

        <section className="block_header">
            <div className="row">
                <div className="col-sm-12 block_header--content">
                    <h4 className="block_header--title">{title}</h4>
                    {
                        link && <Link to={link}>
                            see more <FaAngleRight />
                        </Link>
                    }
                </div>
                <div className="col-sm-12">
                    <div className="block_header--divider" />
                </div>
            </div>
        </section>

    )
};

BlockHeader.defaultProps = {
    title: 'Latest Blogs',
    link:false
};
BlockHeader.propTypes = {
    title: PropTypes.string.isRequired
}

export default BlockHeader;