import {PROFILE_DELETE,PROFILE_PENDING,PROFILE_SUCCESS,PROFILE_ADD} from './types';

const initialState = {
    pending:true,
    profiles:[]
};

export const reducer = (state=initialState,action) => {
    switch(action.type){
        case PROFILE_PENDING:
            return {
                ...state,
                pending:true
            }
        case PROFILE_DELETE:
            return {
                ...state,
                pending:false,
                profiles:state.profiles.filter(profile => profile !== action.payload)
            }
        case PROFILE_SUCCESS:
            return {
                ...state,
                pending:false,
                profiles:action.payload
            }
        case PROFILE_ADD:
            return {
                ...state,
                pending:false
            }
        default:
            return {
                ...state
            }
    }
};

export const getProfilesData = state => state.profiles;