import React, { useState, useEffect } from 'react';
import { ScaleLoader } from 'react-spinners';

import Banner from '../../shared/Banner';
import Carousel from '../../shared/Carousel';
import ArticlesCard from '../../shared/ArticlesCard';
import BlockHeader from '../../shared/BlockHeader';
import {entertainment_articles,sports_articles,love_articles,academic_articles} from '../../../store/data';

/** Redux for data management */

function Articles(props) {
    const [entertainment, setEntertainment] = useState([]);
    const [sports,setSports] = useState([]);
    const [love,setLove] = useState([]);
    const [academic,setAcademic] = useState([]);
    const [loading, setLoading] = useState(true);

   
    useEffect(() => {
        async function loadArticles() {
            let _entertainment = await entertainment_articles().catch(console.log);
            let _sports = await sports_articles().catch(console.log);
            let _love = await love_articles().catch(console.log);
            let _academic = await academic_articles().catch(console.log);

            setEntertainment(_entertainment);setSports(_sports);setLove(_love);setAcademic(_academic);
            
            return setLoading(false);
        };
        loadArticles();
    }, []);

    if (loading) return (
        <div className="centered_loader_component_holder">
            <div className="centered_component_loader">
                <ScaleLoader height="35" width="4" radius="2" margin="2px" color="#009933" />
            </div>
        </div>
    );

    if(entertainment.length === 0 && sports.length === 0 && love.length === 0 && academic.length === 0) return (    
        <Banner message={`Sorry no articles uploaded yet`}/>
    );

    return (
    <React.Fragment>
        {
            entertainment.length >= 3 && <Carousel title={'Entertainment'} data={entertainment} card={'articles'} see_more={false} />
        }
        {
            entertainment.length < 3 && (
                <div className="row">
                    <BlockHeader title={'Entertainment'} />
                    {
                        entertainment.map((ent,index) => (
                            <div className="col-12 col-sm-3 col-md-3 col-lg-3" key={index}>
                                <ArticlesCard data={ent} />
                            </div>
                        ))
                    }
                </div>
            )
        }
        {
            academic.length >= 3 && <Carousel title={`Academic`} data={academic} card={'articles'} see_more={false} />
        }
        {
            academic.length < 3 && (
                <div className="row">
                    <BlockHeader title={'Academic'} />
                    {
                        academic.map((acd, index) => (
                            <div className="col-12 col-sm-3 col-md-3 col-lg-3" key={index}>
                                <ArticlesCard data={acd} />
                            </div>
                        ))
                    }
                </div>
            )
        }
        {
            love.length >= 3 && <Carousel title={`love and relationship`} data={love} card={'articles'} see_more={false} />
        }
        {
            love.length < 3 && (
                <div className="row">
                    <BlockHeader title={'Love and relationship'} />
                    {
                        love.map((love, index) => (
                            <div className="col-12 col-sm-3 col-md-3 col-lg-3" key={index}>
                                <ArticlesCard data={love} />
                            </div>
                        ))
                    }
                </div>
            )
        }
        {
            sports.length >= 3 && <Carousel title={`Sports`} data={sports} card={`articles`} see_more={false} />
        }
        {
            sports.length < 3 && (
                <div className="row">
                    <BlockHeader title={'Sports'} />
                    {
                        sports.map((sprt, index) => (
                            <div className="col-12 col-sm-3 col-md-3 col-lg-3" key={index}>
                                <ArticlesCard data={sprt} />
                            </div>
                        ))
                    }
                </div>
            )
        }
    </React.Fragment>
    )};

Articles.defaultProps = {
    slice: false
};

export default Articles;