import React from 'react';
import { makeStyles, Card, CardActionArea, CardContent} from '@material-ui/core';
//core-components

const useStyles = makeStyles(theme => ({
    card:{
        maxWidth:385,
        marginRight:"10px"
    },
    media: {
        height: 150,
    },
}));

const ArticleCard = ({data:article}) => {
    const classes = useStyles();
    return (
        <Card className={classes.card}>
            <CardActionArea>
                <img src={article.profile} className="article_card--img" alt={article.title} />
                <CardContent>
                    <span className="badge badge-danger">{article.category}</span>

                    <h4 className="card_heading article_card_heading">
                        {article.title}
                    </h4>
                    <div className="article_card--info">

                    <p className="article_card_info--content">
                    {article.writer}
                    </p>

                    <p className="article_card_info--content">
                    {new Date(article.date).toLocaleDateString()}
                    </p>

                    </div>

                </CardContent>

            </CardActionArea>
            </Card>
    );
}

export default ArticleCard;