import React,{useState,useEffect}from 'react';
import {ScaleLoader} from 'react-spinners';
import {Link} from 'react-router-dom';

//core-components
import Carousel from '../../shared/Carousel';
import Banner from '../../shared/Banner';
import {fetch_mobile_adverts,fetch_desktop_adverts} from '../../../store/data';
/** we shall show the place for redux later */

const ViewAdverts = () => {

    const [mobile,setMobile] = useState([]);
    const [desktop,setDesktop] = useState([]);
    const [loading,setLoading] = useState(true);

    useEffect(() => {
        async function loadAdverts(){
            let _mobile = await fetch_mobile_adverts().catch(console.log);
            let _desktop = await fetch_desktop_adverts().catch(console.log);            
            setMobile(_mobile);
            setDesktop(_desktop);
            return setLoading(false); 
    };
        loadAdverts();
    },[]);

    if(loading) return (
        <div className="centered_loader_component_holder">
            <div className="centered_component_loader">
              <ScaleLoader height="35" width="4" radius="2"  margin="2px" color="#009933" />
            </div>
        </div>
    );

    if(mobile.length === 0 && desktop.length === 0) return <Banner message={`Sorry no adverts synced from server`} />
  
    return (
        <React.Fragment>
            {
                mobile.length > 0 && <Carousel data={mobile} card="adverts" title="Mobile adverts" />
            }
            {
                desktop.length > 0 && <Carousel data={desktop} card="adverts" title="Desktop adverts" />
            }
            <div className="row">
                <div className="col-12 col-sm-12 col-md-12 col-ld-12 text-center">
                    <Link to={{
                         pathname:'/admin/advertForm'
                    }}
                    className="btn btn-outline-info">add an advert</Link>
                </div>
            </div>
        </React.Fragment>
    )
};
export default ViewAdverts;