import {EVENTS_PENDING,EVENTS_SUCCESS,EVENT_SUCCESS} from './types';
const initialState = {
    pending:false,
    events:[],
    event:{}
};
export function eventsReducer(state=initialState,action){
    switch(action.type){
         case EVENTS_PENDING:
             return {
                 ...state,
                 pending:true
             }
         case EVENTS_SUCCESS:
             return {
                 ...state,
                 pending:false,
                 events:action.payload
             }
         case EVENT_SUCCESS:
             return {
                 ...state,
                 pending:false,
                 event:action.payload
             }
         default:
             return {
                 ...state
             }
    }
};
export const getEventsData = state => state.events;