import React,{useState,useEffect} from 'react';
import {ScaleLoader} from 'react-spinners';
//core-components
import Banner from '../../shared/Banner';
import Carousel from '../../shared/Carousel';
import {tech_trends,business_trends,entertainment_trends,sports_trends} from '../../../store/data';


function News() {
    const [tech,setTech] = useState([]);
    const [business,setBusiness] = useState([]);
    const [entertainment,setEntertainment] = useState([]);
    const [sports,setSports] = useState([]);

    const [loading,setLoading] = useState(true);

    useEffect(() => {
        async function loadNews(){

            let _tech = await tech_trends().catch(console.log);
            let _business = await business_trends().catch(console.log);
            let _entertainment = await entertainment_trends().catch(console.log);
            let _sports = await sports_trends().catch(console.log);

            setTech(_tech);setBusiness(_business);setEntertainment(_entertainment);setSports(_sports);

            return  setLoading(false); 
        };
        loadNews();
    },[]);

    if(loading) return (
        <div className="centered_loader_component_holder">
            <div className="centered_component_loader">
                <ScaleLoader height="35" width="4" radius="2" margin="2px" color="#009933" />
            </div>
        </div>
    );

    if(tech.length === 0 && business.length === 0 && entertainment.length === 0 && sports.length === 0) return(
        <Banner message={`Sorry no news yet`} />        
    ) 

    return (
        <React.Fragment>
            {
                tech.length > 0 && <Carousel title={`Tech news`} data={tech} card={`news`} />
            }
            {
                business.length > 0 && <Carousel title={`Business news`} data={business} card={`news`} />
            }
            {
                entertainment.length > 0 && <Carousel title={`Entertainment news`} data={entertainment} card={`news`} />
            }
            {
                sports.length > 0 && <Carousel title={`Sports news`} data={sports} card={`news`} />
            }
        </React.Fragment>
    
    )
};



export default News;