import React,{useState,useEffect} from 'react';
import {Route, Switch,Redirect } from "react-router-dom";
import { BeatLoader } from 'react-spinners';

//core-components
import Admin from "../layouts/Admin";
import Login from "./Auth/Login";
import Signup from "./Auth/Signup";
import ForgottePass from "./Auth/ForgottenPass";
import ResetPassword from "./Auth/ResetPassword";
import {verifyToken} from './utils/data';
import routes from '../routes';

const Home = () => {

    const [token] = useState(localStorage.getItem('admin_token'));
    const [loading,setLoading] = useState(true);
    const [redirect,setRedirect] = useState(false);
    
    useEffect(() => {
        async function authVerify(){
            if(!token) {setRedirect(true); setLoading(false);}
            else{
                let res = await verifyToken(token).catch(console.log);
                if(!res) {setRedirect(true); setLoading(false);}
                else{ setLoading(false); }
            };
        };
        authVerify();
    },[token]);

    if(loading) return (
        <div className="centered_loader_component_holder">
            <div className="centered_component_loader">
                  <BeatLoader size="15" margin="2px" color="#009933" />
            </div>
        </div>
    )

    return (
        <Switch>
            <Route path="/admin/login" component={Login} />
            <Route path="/admin/signup" component={Signup} />
            <Route path="/admin/forgotPassword" component={ForgottePass} />
            <Route path="/password/recover/:userId" component={ResetPassword} />
            <Route path="/admin" component={Admin} />
            {
                routes.map((route, index) => (
                    <Route exact path={`/admin/${route}`} component={Admin} key={index}></Route>
                ))
            }
            {/* <Route path="/admin/event/:eventId" component={Admin} />
            <Route path="/admin/user/:userId" component={Admin} />
            <Route path="/admin/product/:prodId" component={Admin} />
            <Route path="/admin/advertForm" component={Admin} />
            <Route path="/admin/feedbacks" component={Admin} />
            <Route path="/admin/hostels" component={Admin} /> */}
            {
                redirect ? <Redirect from="/" to="/admin/login" /> : <Redirect from="/" to="/admin" />
            }
        </Switch>
    );
};

export default Home;