import React from 'react';
import {Link} from 'react-router-dom';
import {ClipLoader} from 'react-spinners';

//core-components
import { Card, CardHeader, CardBody } from '../../Card';
import avatar from '../../../assets/img/faces/marc.jpg';
import { Table, TableHead, TableBody, TableCell, Paper, makeStyles, TableRow } from '@material-ui/core';

const useStyles = makeStyles(theme => ({
    root: {
        width: '100%',
        marginTop: theme.spacing(3),
        overflowX: 'auto',
    },
    table: {
        minWidth: 650,
    },
}));

const View  = ({users,pending}) => {
    const classes = useStyles();
    return (
        <Card>
            <CardHeader color="success">
                <h3 className="card_header--heading text-white">Registered  Users</h3>
            </CardHeader>
            <CardBody>
                {
                    pending ? (
                        <div className="text-center my-20">
                            <ClipLoader size="50" sizeUnit="px" />
                        </div>
                    ) : (
                            <Paper className={classes.root}>
                                <Table className={classes.table}>
                                    <TableHead>
                                        <TableRow>
                                            {
                                                ['profile', 'name', 'email', 'phone number', 'registration date'].map((cell, index) => {
                                                    return (
                                                        <TableCell key={index}>{cell}</TableCell>
                                                    )
                                                })
                                            }
                                        </TableRow>
                                    </TableHead>
                                    <TableBody>
                                        {
                                            users.map((user, index) => {
                                                return (
                                                    <TableRow key={index}>
                                                        <TableCell>
                                                            <img src={
                                                                user['profile'] ? user['profile'] : avatar
                                                            } alt={user['firstname']} className="user_profile" />
                                                        </TableCell>
                                                        <TableCell>
                                                        <Link to={`/admin/user/${user['_id']}`} className="user_link">
                                                        {
                                                            user['firstname'] ? user['firstname'] + ' ' + user['lastname'] : 'not yet updated'
                                                        }
                                                        </Link>
                                                        </TableCell>
                                                        <TableCell>{user['email']}</TableCell>
                                                        <TableCell>
                                                        {user['phoneNumber'] ? user['phoneNumber'] : 'not yet updated'} 
                                                        </TableCell>
                                                        <TableCell>{new Date(user['registration_date']).toDateString()}</TableCell>
                                                    </TableRow>
                                                )
                                            })
                                        }
                                    </TableBody>
                                </Table>
                            </Paper>
                        )
                }
            </CardBody>
        </Card>
        )
};


export default View;