import React, { Component } from 'react';

import axios from 'axios';
import {Redirect,Link} from 'react-router-dom';
import {ClipLoader} from 'react-spinners';

//core-components
import Nav from './Nav';
import signup_image from '../images/signup_image.jpg'

export class Signup extends Component {
    constructor(props){
        super(props);
        this.state = {
            firstname:'',
            lastname:'',
            loading:false,
            email:'',
            password:'',
            confirm_pass:'',
            profile:'',
            role:'systems architect',
            action:'signup',
            error:''
        }
    };

    onChange = e => {
        if(e.target.name === 'profile') return this.setState({
            [e.target.name]:e.target.files[0]
        });
        return this.setState({
            [e.target.name]:e.target.value
        });
    };

    onSubmit = async e => {
        e.preventDefault();
        this.setState({error:''});

        if(!this.state.firstname || !this.state.lastname || !this.state.email || !this.state.password 
            || !this.state.role || !this.state.confirm_pass) return this.setState({error:'Fill out all the fields',action:'try again'});
        
        /** password equivalence */
        
        if(this.state.password !== this.state.confirm_pass) return this.setState({
            'error':'Passwords must be equal'
        });

        if(this.state.password.length < 6) return this.setState({
            error:"Minimum characters of six for passwords"
        })

        let formData = new FormData();
        formData.append('firstname',this.state.firstname);formData.append('lastname',this.state.lastname);
        formData.append('email',this.state.email);formData.append('password',this.state.password);
        formData.append('role',this.state.role);formData.append('profile',this.state.profile);
        
        this.setState({action:'sending....',loading:true});

        let res = await axios.post(
            `https://karuapi.herokuapp.com/api/admin/signup`,
            formData,
            {
                headers:{'Content-Type':'multipart/form-data'}
            }
        ).catch(console.log);

        if(!res.data.success) return this.setState({error:res['data']['message'],action:'try again',loading:false});
        if(res.data.success){
            return this.setState({
                firstname:'',lastname:'',email:'',password:'',profile:'',redirect:true,loading:false
            })
        }
    };

    
    render() {
        const {redirect} = this.state;
        if(redirect) return <Redirect to={'/'} />
        return (
            <section>
                <Nav btn="signup" />
            <div className="auth_container container">
            <div className="row">                
                <div className="d-none d-sm-block d-lg-block col-sm-6 col-lg-6 col-md-6"> 
                    <div className="auth_image_container" style={{backgroundImage:`url(${signup_image})`}}>
                        <div className="auth_image_inner">
                            <div className="auth_content">
                                <h3 className="auth_content_heading">allComrades Admin Dashboard</h3>
                                <p className="auth_content_text">Already have an account <Link to={'/'}>login</Link></p>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="col-sm-6 col-12 col-md-6 col-lg-6">
                        <form onSubmit={this.onSubmit}>
                            {
                                this.state.error && <p className="text-danger">
                                    {this.state.error}
                                </p>
                            }

                            <div className="form-group">
                                <label htmlFor="firstname">Firstname</label>
                                <input type="text" 
                                className="form-control"
                                name="firstname"
                                placeholder="Enter your firstname"
                                onChange={this.onChange}
                                value={this.state.firstname}
                                id="firstname"
                                />
                            </div>

                            <div className="form-group">
                                <label htmlFor="lastname">Lastname</label>
                                <input type="text"
                                    className="form-control"
                                    name="lastname"
                                    placeholder="Enter your lastname"
                                    onChange={this.onChange}
                                    value={this.state.lastname}
                                />
                            </div>

                            <div className="form-group">
                                <label htmlFor="email">Email</label>
                                <input type="email"
                                    id="email"
                                    className="form-control"
                                    name="email"
                                    placeholder="Enter your email address"
                                    onChange={this.onChange}
                                    value={this.state.email}
                                />
                            </div>

                            <div className="form-group">
                                <label htmlFor="profile">Profile picture</label>
                                <input type="file"
                                    className="form-control"
                                    name="profile"
                                    onChange={this.onChange}
                                    id="profile"
                                />
                            </div>

                            <div className="form-group">
                                <label htmlFor="role">Role</label>
                                <select className="form-control" name='role' onChange={this.onChange} value={this.state.role}>
                                    <option value="systems architect">systems architect</option>
                                    <option value="content writer">content writer</option>
                                </select>
                            </div>

                            <div className="form-group">
                                <label htmlFor="password">Password</label>
                                <input type="password"
                                name="password"
                                id="password"
                                className="form-control"
                                placeholder="Enter your password"
                                value={this.state.password}
                                onChange={this.onChange}                                
                                />
                            </div>

                            <div className="form-group">
                                <label htmlFor="confirm_pass">Confirm Password</label>
                                <input type="password"
                                    name="confirm_pass"
                                    id="confirm_pass"
                                    className="form-control"
                                    placeholder="Confirm your password"
                                    value={this.state.confirm_pass}
                                    onChange={this.onChange}
                                />
                            </div>

                            <div className="form-group">
                                
                                {
                                    this.state.loading ?  <ClipLoader size="20"  sizeUnit="px" /> : (
                                        <input type='submit' className='btn btn-outline-success admin_action_btn' value={this.state.action}/> 
                                    )
                                }
                            </div>
                            <p className="d-block d-sm-none d-lg-none d-md-none auth_content_text">
                            Already have an account <Link to={'/'}>login </Link>
                            </p>
                        </form>
                </div>
            </div>
        </div>
        </section>
        )
    }
}

export default Signup
