import React,{useEffect,useState} from 'react';
import {TabContent,TabPane,Nav,NavItem,NavLink} from 'reactstrap';
import {BeatLoader} from 'react-spinners';
import classnames from 'classnames';
import {IoIosCall} from 'react-icons/io';
import Slider from 'react-slick';

import {getSpecificHostel} from '../../utils/data';
import Banner from '../../shared/Banner';
import {eventSlider as settings } from '../../../data';

const HostelPage = ({match}) => {
    
    const _hostelId = match.params.hostelId;
    const [loading,setLoading] = useState(true);
    const [hostel,setHostel] = useState({});
    const [error,setError] = useState(false);
    const [activeTab,setActiveTab] = useState('1');


    useEffect(() => {
       const load_data = async () => {
           let {success,message} = await getSpecificHostel(_hostelId).catch(console.log);
           if(!success) setError(true);
           setHostel(message);
           return setLoading(false);
       };
       load_data();
    },[_hostelId]);

    const toggle = tab => {
        if(tab !== activeTab) return setActiveTab(tab);
    }

    if(loading) return (
        <div className="centered_loader_component_holder">
            <div className="centered_component_loader">
                <BeatLoader size="15" margin="2" />
            </div>
        </div>
    );

    if(error) return <Banner message={`Sorry some error occurred`} />
    
    const slideshow = hostel.profiles.map((profile,index) => {
        return (
            <>
                <div className="d-block d-sm-none d-md-none d-lg-none">
                    <div className="hostel_mobile" key={index}>
                        <div className="hostel_page_slideshow">
                            <img src={profile} className="hostel_page_image" alt={`${hostel.name}`} />
                            <div className="hostel_page_inner" />
                        </div>
                    </div>
                </div>
                <div className="d-none d-sm-block d-md-block d-lg-block">
                    <div className="hostel_desktop" key={index}>
                        <div className="hostel_page_slideshow">
                            <img src={profile} className="hostel_page_image" alt={`${hostel.name}`} />
                            <div className="hostel_page_inner" />
                        </div>
                    </div>
                </div>
            </>
        )
    })


    return (
        <section>

        <div className="container">
            <div className="row">
                <div className="col-12 col-sm-6 col-md-6 col-lg-6">
                    <Slider {...settings}>
                        {slideshow}
                    </Slider>
                </div>
                <div className="col-12 col-sm-6 col-md-6 col-lg-6">
                <Nav tabs>
                <NavItem>
                <NavLink active={classnames({ active: activeTab === '1' })} onClick={() => toggle('1')}>
                Features
                </NavLink>
                </NavItem>
                <NavItem>
                <NavLink active={classnames({ active: activeTab === '2' })} onClick={() => toggle('2')}>
                Description and contacts
                </NavLink>
                </NavItem>
                </Nav>
                <TabContent activeTab={activeTab}>
                    <TabPane tabId="1">

                        <h4 className="hostel_page--details">Name: {hostel.name} hostels</h4>
                        <h4 className="hostel_page--details">Location: {hostel.location}</h4>
                        <h4 className="hostel_page--details">Type: {hostel.type}</h4>
                        {
                            hostel.deposit && <h4 className="hostel_page--details">Deposit: Kshs {parseInt(hostel.deposit).toLocaleString()} </h4>
                        }                        
                        <h4 className="hostel_page--details">Rent: Kshs {parseInt(hostel.rent).toLocaleString()}</h4>
                    </TabPane>
                    <TabPane tabId="2">
                        <h4 className="hostel_page--title">Description</h4>
                        <p className="hostel_page--details">{hostel.description}</p>
                        <h4 className="hostel_page--title">Contact details</h4>
                        <p className="hostel_page--details"><IoIosCall /> {parseInt(hostel.caretaker).toLocaleString()}</p>
                    </TabPane>
                </TabContent>
                </div>
            </div>
        </div>

        


        </section>
    )
};


export default HostelPage;