import React, { useState, useEffect } from 'react';
import { ClipLoader } from 'react-spinners';
import Slick from 'react-slick';
//core-components
import { GridContainer, GridItem } from '../../Grid';
import avatar from '../../../assets/img/faces/marc.jpg';
import {fetch_product,fetch_related_products} from '../../../store/data';
import Carousel from '../../shared/Carousel';
import {carouselSettings as slickSettings} from '../../../data';

const Product = (props) => {

    const [loading, setLoading] = useState(true);
    const [product, setProduct] = useState({});
    const [related,setRelated] = useState([]);
    const { prodId } = props.match.params;

    const showContacts = () => {
        return alert('how should we show the contacts of seller')
    }

    useEffect(() => {
        async function loadProduct() {
            let _product = await fetch_product(prodId).catch(console.log);
            let _products = await fetch_related_products(prodId).catch(console.log);
            setProduct(_product);setRelated(_products);
            return setLoading(false);
        };
        loadProduct();
    }, [prodId]);

    if (loading) return (
        <div className="text-center my-20">
            <ClipLoader size="30" sizeUnit="px" />
        </div>
    );

    const slides_to_show = product.images.map((image, index) => {
        return (
            <>
            <div className="d-block d-sm-none d-md-none d-lg-none">
                <div className="element_mobile" key={index}>
                    <div className="element_content_slideshow" style={{ backgroundImage: `url(${image})` }}>
                        <div className="element_content_slideshow_inner"></div>
                    </div>
                </div>
            </div>
            <div className="d-none d-sm-block d-md-block d-lg-block">
                <div className="element_desktop" key={index}>
                    <div className="element_content_slideshow" style={{ backgroundImage: `url(${image})` }}>
                        <div className="element_content_slideshow_inner"></div>
                    </div>
                </div>
            </div>
            </>
        )
    });

    return (
        <>
            <GridContainer>
                <GridItem xs={12} md={12} sm={12}>
                    <div className="container">
                        <h3 className="product_card-heading">{product['name']}</h3>
                        {
                            product.images.length > 1 ? (
                                <Slick {...slickSettings}>
                                    {slides_to_show}
                                </Slick>
                            ) : (
                                    <>
                                        {/** for mobile */}
                                        <div className="d-block d-sm-none d-md-none d-lg-none">
                                            <div className="mobile_element_content_slide" style={{
                                                backgroundImage: `url(${product.images[0]})`
                                            }}>
                                                <div className="element_content_slideshow_inner" />
                                            </div>
                                        </div>
                                        {/** for desktop */}
                                        <div className="d-none d-sm-block d-md-block d-lg-block">
                                            <div className="desktop_element_content_slide" style={{
                                                backgroundImage: `url(${product.images[0]})`
                                            }}>
                                                <div className="element_content_slideshow_inner" />
                                            </div>
                                        </div>
                                    </>
                                )
                        }
                        <div className="productDesc">
                            <h4 className="product_card-heading">Product Description</h4>
                            <p className="product_card--content">
                                {product.description}
                            </p>
                        </div>
                        <div className="product_card-seller">
                            <h4 className="product_card-heading">Seller info</h4>
                            <div className="media">
                                <img src={
                                    product['seller']['profile'] ? product['seller']['profile'] : avatar
                                } className="mr-3 product_card_seller-profile" alt={product['seller']['firstname']} />
                                <div className="media-body">
                                    <h4 className="media-heading product_card_seller-name">
                                        {
                                            product['seller']['firstname'] + ' ' + product['seller']['lastname']
                                        }
                                    </h4>
                                    <button className="btn btn-success" onClick={() => showContacts()}>connect</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </GridItem>
            </GridContainer>
            <GridContainer>
                <div className="container mt-20">
                {
                    related.length > 0 && <Carousel data={related} title={`Related products`} card={'products'} />
                }
                </div>
            </GridContainer>
        </>
    )
}



export default Product;