import React from 'react';
import axios from 'axios';
import {Redirect,Link} from 'react-router-dom';
import login_image from '../images/login_image.jpg';
import {ClipLoader} from 'react-spinners';

//core-components
import Nav from './Nav';

class Login extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            error:'',
            email:"",
            password:"",
            loading:false,
            adminId:'',
            action:'Login',
            redirect:false
        }
    };


    onChange = (e) => {
        return this.setState({
            [e.target.name] : e.target.value
        })
    };
    
    onSubmit = async  e => {
        e.preventDefault();
        this.setState({error:''});
        if(!this.state.email|| !this.state.password){
            return this.setState({
                error:"You must fill out all the fields"
            })
        };

        const data = {email:this.state.email,password:this.state.password};

        this.setState({action:"logging",loading:true});

        let res = await axios.post(
            'https://karuapi.herokuapp.com/api/admin/login'            
            ,data,{
            headers:{'Content-Type':'application/json'}
        }).catch(console.log);
        
        if(!res.data.success) return this.setState({error:res.data.message,action:"Try Again",loading:false});

        if(res.data.success) {
            localStorage.setItem('admin_token',res.data.message);
            this.setState({error:'',email:'',password:'',action:'Login',redirect:true,loading:false});
        }
    }
    render(){
        if(this.state.redirect) return <Redirect to="/admin/dashboard"  />
        return(
        <section>
          <Nav btn="login"/>
            <div className="auth_container container">
                <div className="row">
                    <div className="d-none d-sm-block d-lg-block col-sm-6 col-lg-6">
                        <div className="auth_image_container" style={{ backgroundImage: `url(${login_image})` }}>
                            <div className="auth_image_inner">
                                <div className="auth_content">
                                    <h3 className="auth_content_heading">allComrades Admin Dashboard</h3>
                                    <p className="auth_content_text">don't have an account <Link to={'/admin/signup'}>signup</Link></p>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-sm-6 col-12 col-md-6 col-lg-6">
                           <form onSubmit={this.onSubmit}>
                               {
                                   this.state.error && <p className="text-danger">
                                       {this.state.error}
                                   </p>
                               }
                               <div className="form-group">
                                   <label htmlFor="email">Email</label>
                                   <input type="email"
                                     className="form-control"
                                     name="email"
                                     value={this.state.email}
                                     placeholder="Enter your email"
                                     onChange={this.onChange}
                                    />
                               </div>
                                <div className="form-group">
                                    <label htmlFor="password">Password</label>
                                    <input type="password"
                                    className="form-control"
                                    name="password"
                                    placeholder="Enter your password"
                                    value={this.state.password}
                                    onChange={this.onChange}
                                    />
                                </div>
                                <div className="form-group">
                                    
                                    {
                                        this.state.loading ? <ClipLoader size="20" sizeUnit="px" /> : (
                                            <input type="submit" className="btn btn-outline-success admin_action_btn" value={this.state.action} />
                                        )
                                    }
                                    <Link className="auth_content_text text-right" to={`/admin/forgotPassword`}>
                                        forgot password
                                    </Link>
                                </div>
                                <p className="d-block d-sm-none d-lg-none d-md-none auth_content_text" style={{marginTop:"5px"}}>
                                 don't have an account <Link to={'/admin/signup'}>signup</Link>                                                                 
                                </p>
                           </form>
                       </div>
                    </div>
            </div>
        </section>
        )
    }
};

export default Login;