import {PROFILE_DELETE,PROFILE_PENDING,PROFILE_SUCCESS,PROFILE_ADD} from './types';

export const profilePending = () => {
    return {
        type:PROFILE_PENDING
    }
};

export const profileSuccess = (data) => {
    return {
        type:PROFILE_SUCCESS,
        payload:data
    }
};

export const profileDelete = (data) => {
    return {
        type:PROFILE_DELETE,
        payload:data
    }
};

export const profileAdd = (data) => {
    return {
        type:PROFILE_ADD,
        payload:data
    }
}