import React, { useState,useEffect } from 'react';
import { BeatLoader } from 'react-spinners';
import {connect} from 'react-redux';

import Carousel from '../../shared/Carousel';
import GalleryCard from '../../shared/GalleryCard';
import {fetchProfiles,getProfilesData,addProfile} from '../../../store/profiles';

const HostelImages = (props) => {
    
    const [_id] = useState(props.location.state._id);
    const [_profiles,setProfiles] = useState(null);
    const [loading,setLoading] = useState(true);
    const [profile,setProfile] = useState('');
    const [error,setError] = useState(false);
    const [action] = useState('upload');
    const {pending,profiles,fetchProfiles,addProfile} = props;

    useEffect(() => {
        const load_profiles = () => {
           fetchProfiles(_id);
           if(!pending){
               setProfiles(profiles);
               return setLoading(false);
           };       
        };
        load_profiles();
    },[profiles]);

    const onchange = e => {
        return setProfile(e.target.files[0]);
    };
    const onsubmit = e => {

        e.preventDefault();
        setError('');
        if(!profile) return setError('You must first upload an image');
        const form_data = new FormData();
        form_data.append('profile',profile);
        return addProfile(_id,form_data);
    }

    if(loading) return (
        <div className="text-center">
            <BeatLoader margin="2" size="15" color="#009933" />
        </div>
    );

    return (
        <section>
            <div className="container-fluid">
                {
                    _profiles.length >= 3 && <Carousel data={_profiles} _id={_id} card="gallery" title="Uploaded Profiles" />
                }
                {
                    _profiles.length < 3 && (
                        <div className="row">
                            {
                                _profiles.map((profile,index) => (
                                    <div className="col-12 col-sm-3 col-md-3 col-lg-3" key={index}>
                                          <GalleryCard data={profile} _id={_id} />
                                    </div>
                                ))
                            }
                        </div>
                    )
                }
                <div className="row">
                    
                    <div className="col-12 col-sm-12 col-md-12 col-lg-12">
                        <h5 className="card_heading" style={{marginBottom:'10px'}}>Add some more image</h5>
                        <form onSubmit={(event) => onsubmit(event)}>
                            {
                                error && (<div className="form-group">
                                    <p className="text-danger">{error}</p>
                                </div>)
                            }
                        <div className="form-group">
                            <input type="file" 
                            className="form-control"
                            name="profile"
                            onChange={onchange}
                            placeholder="Select an Image"
                            />
                        </div>
                        <div className="form-group">
                            <input type="submit" className="btn btn-sm btn-outline-success" value={action} />
                        </div>
                        </form>
                    </div>
                </div>
            </div>
        </section>
    )
};

const mapStateToProps = state => ({
    pending:getProfilesData(state).pending,
    profiles:state.profiles.profiles
});

const dispatchStateToProps = {
    fetchProfiles,
    addProfile
}

export default connect(mapStateToProps,dispatchStateToProps)(HostelImages);