import React,{useState,useEffect} from 'react';
import {ScaleLoader} from 'react-spinners';
//data  source

//core-components
import Carousel from '../../shared/Carousel';
import BlockHeader from '../../shared/BlockHeader';
import EventCard from '../../shared/EventCard';
import Banner from '../../shared/Banner';
import {upcoming_events,past_events,current_events} from '../../../store/data';


function Events() {
    const [upcoming,setUpcoming] = useState([]);
    const [past,setPast] = useState([]);
    const [current,setCurrent] = useState([]);

    const [loading,setLoading] = useState(true);
    let upcoming_to_show = '';
    let current_to_show = '';
    let past_to_show = '';

    useEffect(() => {
        async function loadEvents(){
            let _upcoming = await upcoming_events().catch(console.log);
            let _past = await past_events().catch(console.log);
            let _current = await current_events().catch(console.log);

            setUpcoming(_upcoming);setPast(_past);setCurrent(_current);
            return  setLoading(false);
        };
        loadEvents();
    },[]);

    if(loading) return (
        <div className="centered_loader_component_holder">
            <div className="centered_component_loader">
                <ScaleLoader height="35" width="4" radius="2" margin="2px" color="#009933" />
            </div>
        </div>
    );
    
    if(upcoming.length === 0 && past.length === 0 && current.length === 0)return (
         <Banner message={'No upcoming events synced from the database'} />
    );
    
    if(upcoming.length >= 3){
        upcoming_to_show = <Carousel data={upcoming} card="events" title="Upcoming events" />
    }else if(upcoming.length > 0 && upcoming.length < 3){
        upcoming_to_show = <div className="row">
            <div className="col-12 col-sm-12"> <BlockHeader title="Upcoming events" /> </div>
            {
                upcoming.map((event,index) => (
                    <div className="col-12 col-sm-4 col-md-4" key={index}>
                        <EventCard data={event} />
                    </div>
                ))
            }
        </div>
    };

    if (current.length >= 3) {
        current_to_show = <Carousel data={current} card="events" title="Current events" />
    } else if (current.length > 0 && current.length < 3) {
        current_to_show = <div className="row">
            <div className="col-12 col-sm-12"> <BlockHeader title="Current events"/> </div>
            {
                current.map((event, index) => (
                    <div className="col-12 col-sm-4 col-md-4" key={index}>
                        <EventCard data={event} />
                    </div>
                ))
            }
        </div>
    };

    if (past.length >= 3) {
        past_to_show = <Carousel data={past} card="events" title="Past events" />
    } else if (past.length > 0 && past.length < 3) {
        past_to_show = <div className="row">
            <div className="col-12 col-sm-12"> <BlockHeader title="Past events" /> </div>
            {
                past.map((event, index) => (
                    <div className="col-12 col-sm-4 col-md-4" key={index}>
                        <EventCard data={event} />
                    </div>
                ))
            }
        </div>
    }

    return (
        <React.Fragment>
            {
                upcoming_to_show
            }
            {
                current_to_show
            }
            {
                past_to_show
            }
        </React.Fragment>

    )
    
   
};



export default Events;