import React,{Component} from 'react';
import {BeatLoader} from 'react-spinners';
import {Link} from 'react-router-dom';

import {uploadHostel,hostel_locations,hostel_types,getSpecificHostel,updateHostel} from '../../utils/data'; 

class HostelForm extends Component {
    
    fileObj = [];
    formData = new FormData();

    state = {
       name:'',
       location:'Kagochi',
       type:'Single room',
       deposit:'',
       rent:'',
       profiles:[],
       caretaker:'',
       description:'',
       fileArray:[],
       action:'upload',
       complete:false,
       hostelId:'',
       edit:false,
       pending:true,
       error:''
    };

    componentDidMount = async () => {
        if(this.props.location.state){
            //we get the data from api
            let {_id} = this.props.location.state;
            let {message} = await getSpecificHostel(_id).catch(console.log);
            let {name,location,type,deposit,rent,caretaker,description} = message;
            return this.setState({
                name,location,type,deposit : deposit ? deposit : '',rent,caretaker,description,hostelId:_id,
                edit:true,action:'update',pending:false
            });
        };
        return this.setState({pending:false});
    };

    uploadMultipleImages = e => {
        this.fileObj.push(e.target.files);
        for(let i=0;i<this.fileObj[0].length;i++){
            this.state.fileArray.push(URL.createObjectURL(this.fileObj[0][i]));
        };
        //the .push  method of an array fails because it returns the length of the array itself.
        //the .concat method on the other hand returns the updated array
        this.setState({profiles:e.target.files});
        for(let i = 0; i<e.target.files.length;i++){
            this.formData.append('profiles',e.target.files[i])
        };

    }

    onChange = e => {
        return this.setState({[e.target.name]:e.target.value});
    };


    onSubmit = async e => {
        e.preventDefault();
        this.setState({error:''});
        let {name,location,type,deposit,rent,caretaker,description,profiles,hostelId,edit} = this.state;

        
        //we got all the fields
        let hostel = { name, location, type, deposit: deposit ? deposit : null, rent, caretaker, description };
        
        if(!edit){
        if (!name || !location || !type || !rent || !caretaker || !description || profiles.length === 0) return this.setState({
            error: 'Fill out all fields accordingly'
        });      
        Object.keys(hostel).forEach(key => this.formData.append(key,hostel[key]));
        this.setState({action:'uploading'});
        let {success,message} = await uploadHostel(this.formData).catch(console.log);
        if(!success) return this.setState({error:message});
        return this.setState({
            name:'',location:'',type:'',deposit:'',rent:'',caretaker:'',description:'',profiles:'',action:'upload',
            fileArray:[],complete:true
         });
        }else{
        //we just push the update
        if(!name || !location || !type || !rent || !caretaker || !description) return this.setState({
            error:'Fill out all the fields correctly'
        });
        
        this.setState({action:'updating'});
        let {message} = await updateHostel(hostelId,hostel).catch(console.log);
        return this.setState({
            name:message.name,location:message.location,type:message.type,deposit:message.deposit ? message.deposit : '',
            rent:message.rent,caretaker:message.caretaker,description:message.description,action:'update',complete:true
        })
        }

    };

    render(){
        const {pending} = this.state;
        if(pending) return (
            <div className="centered_loader_component_holder">
                <div className="centered_component_loader">
                    <BeatLoader size="15" margin="2" color="#009933" />
                </div>
            </div>
        );

        return (
            <section>
                <div className="container">
                    <div className="row">
                        <div className="col-12 col-sm-12 col-md-12 col-lg-12">
                            {
                                this.state.edit && (
                                    <Link to={
                                        {
                                            pathname:'/admin/hostel_form/edit_images',
                                            state:{_id:this.state.hostelId}
                                        }
                                    } className="btn btn-sm btn-outline-success">edit images</Link>
                                )
                            }
                            <form onSubmit={this.onSubmit}>
                                {
                                    this.state.error && <div className="form-group">
                                        <p className='text-danger'>{this.state.error}</p>
                                    </div>
                                }
                                <div className="form-group">
                                    <label htmlFor="name">Hostel name</label>
                                    <input type="text"
                                    placeholder='Enter the name of the hostel'
                                    className="form-control"
                                    name="name"
                                    value={this.state.name}
                                    onChange={this.onChange}
                                    />
                                </div>   
                                <div className="form-group">
                                    <label htmlFor="location">Location</label>
                                    <select name="location" value={this.state.location} onChange={this.onChange} className="form-control">
                                       {
                                           hostel_locations
                                           .map((location,index) => <option value={location} key={index}>{location}</option>)
                                       }
                                    </select>
                                </div>
                                <div className="form-group">
                                    <label htmlFor="type">Type</label>
                                    <select name="type" value={this.state.type} onChange={this.onChange} className="form-control">
                                        {
                                           hostel_types
                                           .map((type,index) => <option value={type} key={index}>{type}</option>)
                                        }
                                    </select>
                                </div>
                                <div className="form-group">
                                    <label htmlFor="deposit">Deposit (optional)</label>
                                    <input type="text"
                                    placeholder="Deposit required to pay"
                                    className="form-control"
                                    name="deposit"
                                    value={this.state.deposit}
                                    onChange={this.onChange}
                                     />
                                </div> 
                                <div className="form-group">
                                    <label htmlFor="rent">Rent</label>
                                    <input type="text"
                                    placeholder="Monthly rent for the hostels"
                                    className="form-control"
                                    name="rent"
                                    value={this.state.rent}
                                    onChange={this.onChange}
                                    />
                                </div>
                                <div className="form-group">
                                    <label htmlFor="caretaker">Caretaker Info</label>
                                    <input type="text"
                                    placeholder="Caretakers option phone number"
                                    className="form-control"
                                    name="caretaker"
                                    value={this.state.caretaker}
                                    onChange={this.onChange}
                                    />
                                </div>
                                <div className="form-group">
                                    <label htmlFor="description">Description</label>
                                    <textarea className="form-control"
                                    placeholder="Put a short description on Wi-Fi, security, Hot shower, e.t.c "
                                    name="description"
                                    value={this.state.description}
                                    onChange={this.onChange}
                                    rows={5}
                                    cols={40}
                                     ></textarea>
                                </div>
                                {
                                    this.state.fileArray.length > 0 && (
                                        <div className="form-group multi-preview">
                                            <label htmlFor="profiles">Uploaded Photos</label>
                                            <br />
                                            {
                                                this.state.fileArray.map((file,index) => <img src={file} key={index} className="hostel_form--image" alt="hostel" />)
                                            }
                                        </div>
                                    )
                                }
                                {
                                    !this.state.edit && (
                                        <div className="form-group">
                                            <label htmlFor="profiles">Upload photo</label>
                                            <input type="file" multiple id="profiles"
                                                name="profiles" onChange={this.uploadMultipleImages}
                                                className="form-control"
                                            />
                                        </div>
                                    )
                                }                                
                                <div className="form-group">
                                    <input type="submit" className="btn btn-sm btn-outline-success links" value={this.state.action} />
                                    {
                                        this.state.complete && <Link to={`/admin/hostels`} className="btn btn-sm btn-outline-success">hostels page</Link>
                                    }
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </section>
        )
    }
};


export default HostelForm;