import axios from 'axios';
const URL = `https://karuapi.herokuapp.com/api`;

export const fetch_articles = async () => {
    await axios.post(`${URL}/articles/loadArticles`).catch(console.log);
    let data = await axios.get(`${URL}/articles/fetchArticles`).catch(console.log);
    let result = [...new Set(data.data.message.sort((a,b) => new Date(b.date) > new Date(a.date)))];
    return result;
};
/** the categories entertainment,sports,love and relationship and social shall change to academics */
export const entertainment_articles = async () => {
    let articles = await fetch_articles().catch(console.log);
    let _entertainment = articles.filter(article => article.category === 'Entertainment');
    return _entertainment;
};
export const sports_articles = async () => {
    let articles = await fetch_articles().catch(console.log);
    let _sports = articles.filter(article => article.category === 'Sports');
    return _sports;
};
export const love_articles = async () => {
    let articles = await fetch_articles().catch(console.log);
    let _love = articles.filter(article => article.category === 'Love and Relationship');
    return _love;
};
export const academic_articles = async () => {
    let articles = await fetch_articles().catch(console.log);
    let _academics = articles.filter(article => article.category === 'Academics');
    return _academics;
};

      /**  Events Space */

const fetch_events = async () => {
    let data = await axios.get(`${URL}/events/fetchEvents`).catch(console.log);
    let result = data.data.message;
    result = result.sort((a,b) => new Date(a.date) - new Date(b.date));
    return result;
};
export const fetch_event = async (event_id) => {
    let data = await axios.get(`https://karuapi.herokuapp.com/api/events/fetchEvent/${event_id}`).catch(console.log);
    let result = data.data.message;
    return result;
};
export const fetch_related_events = async (event_id) => {
    let events = await fetch_events().catch(console.log);
    let result = events.filter(event => event._id !== event_id);
    result = result.sort((a,b) => new Date(a.date) - new Date(b.date));
    return result;
}
/** getting the upcoming events and the past events */
export const upcoming_events = async () => {
    let events = await fetch_events().catch(console.log);
    let upcoming = events.filter(event => new Date(event.date).getTime() > new Date().getTime());
    upcoming.sort((a,b) => new Date(a.date) - new Date(b.date));
    return upcoming;
};
export const current_events = async () => {
    let events = await fetch_events().catch(console.log);
    let current_events = events.filter(event => new Date(event.date).getDay() === new Date().getDay());
    current_events.sort((a,b) => new Date(a.date) - new Date(b.date));
    return current_events;
};
export const past_events = async () => {
    let events = await fetch_events().catch(console.log);
    let past = events.filter(event => new Date(event.date).getTime() < new Date().getTime());
    past.sort((a, b) => new Date(a.date) - new Date(b.date));
    return past;
};

/** The product Space */

/** getting for the marketPlace */
const fetch_products = async () => {
    let data = await axios.get(`${URL}/marketPlace/products/fetchProducts`).catch(console.log);
    return data['data'].message;
};
export const fetch_product = async(product_id) => {
    let product = await axios.get(`${URL}/marketPlace/fetchProduct/${product_id}`).catch(console.log);
    return product.data.message;
};
export const fetch_related_products = async (product_id) => {
    let products = await fetch_products().catch(console.log);
    products = products.filter(product => product._id !== product_id);
    let result = products.sort((a,b) => new Date(a.uploadedAt) - new Date(b.uploadedAt));
    return result;
}
