/** authorization space */
export const authenticate_token = async (token) => {
    let result = await axios.post(`${URL}/admin/decodeToken`,null,{
        headers:{'Authorization':`Bearer ${token}`}
    }).catch(console.log);
    if(!result.data.success) return false;
    return result.data.message;
};


/** Notifications space fetch notifications */
export const fetch_notifications = async (token) => {
    let {_id} = await authenticate_token(token).catch(console.log);
    let notifs = await axios.get(`${URL}/notifications/fetchNotifications`).catch(console.log);
    notifs = notifs.data.message.filter(notif => notif.receiver === _id);
    return notifs;
};

/** the adverts section */
export const fetch_adverts = async () => {
    let result = await axios.get(`${URL}/adverts/fetchAdverts`).catch(console.log);
    result = result.data.message;
    return result;
};

//for mobile adverts
export const fetch_mobile_adverts = async () => {
    let adverts = await fetch_adverts().catch(console.log);
    let mobile_adverts = adverts.map(advert => {
        let mobile_advert = {};
        mobile_advert['description'] = advert['description'];
        mobile_advert['btnInfo'] = advert['btnInfo'];
        mobile_advert['advert'] = advert['mobile_advert'];
        mobile_advert['division'] = advert['division'];
        mobile_advert['type'] = advert['type'];
        mobile_advert['link'] = advert['link'];
        mobile_advert['tagged'] = advert['tagged'];
        mobile_advert['_id'] = advert['_id'];
        return mobile_advert;
    });
    return mobile_adverts;
};

//for desktop adverts
export const fetch_desktop_adverts = async () => {
    let adverts = await fetch_adverts().catch(console.log);
    let desktop_adverts = adverts.map(advert => {
        let desktop_advert = {};
        desktop_advert['description'] = advert['description'];
        desktop_advert['btnInfo'] = advert['btnInfo'];
        desktop_advert['advert'] = advert['advert'];
        desktop_advert['division'] = advert['division'];
        desktop_advert['type'] = advert['type'];
        desktop_advert['link'] = advert['link'];
        desktop_advert['tagged'] = advert['tagged'];
        desktop_advert['_id'] = advert['_id'];
        return desktop_advert;
    });
    return desktop_adverts;
};

//deleting an advert;
export const delete_advert = async (advert_id) => {
      let result = await axios.delete(`${URL}/adverts/deleteAdvert/${advert_id}`).catch(console.log);
      return result.data.success;
};