import { ARTICLES_PENDING, ARTICLES_SUCCESS } from './types';
const initialState = {
    pending: false,
    articles:[]
};
export function articlesReducer(state = initialState, action) {
    switch (action.type) {
        case ARTICLES_PENDING:
            return {
                ...state,
                pending: true
            }
        case ARTICLES_SUCCESS:
            return {
                ...state,
                pending: false,
                articles: action.payload
            }
        default:
            return {
                ...state
            }
    }
};
export const getArticlesData = state => state.articles;