import React,{useState,useEffect} from 'react';
import {ClipLoader} from 'react-spinners';
import {makeStyles} from '@material-ui/core';

/** the components */
import {GridItem,GridContainer} from '../../Grid';
import {Card,CardHeader,CardIcon,CardFooter} from '../../Card';
import { MdPeople,MdWhatshot} from 'react-icons/md';
import {FaBullhorn,FaGlobeAfrica} from 'react-icons/fa';
import {Accessibility,Satellite,LocalOffer} from "@material-ui/icons";
import styles from '../../../assets/jss/material-dashboard-react/views/dashboardStyle';
import {loadGeneral} from '../../utils/data';

const useStyles = makeStyles(styles);

const General = () => {
    const classes = useStyles();
    const [data,setData] = useState([]);
    const [loading,setLoading] = useState(true);

    useEffect(() => {
        async function loadData(){
            let {message} = await loadGeneral().catch(console.log);
            setData(message);
            return setLoading(false);
        };
        loadData();
    },[]);

    if(loading) return (
        <div className="text-center my-20">
                <ClipLoader size="25" sizeUnit="px" color="#009933" />
        </div>
    );

    return (
        <GridContainer>
            <GridItem xs={12} sm={6} md={3}>
                <Card>
                    <CardHeader color="success" stats icon>
                        <CardIcon color="success">
                            <MdPeople />
                        </CardIcon>
                        <p className={classes.cardCategory} >Registered Users</p>
                        <h3 className={classes.cardTitle}>
                            {data[0]}
                        </h3>
                    </CardHeader>
                    <CardFooter stats>
                        <div className={classes.stats}>
                            <Accessibility />
                            Users in our servers
                        </div>
                    </CardFooter>
                </Card>
            </GridItem>
            <GridItem xs={12} sm={6} md={3}>
                <Card>
                    <CardHeader color="info" stats icon>
                        <CardIcon color="info">
                            <MdWhatshot />
                        </CardIcon>
                        <p className={classes.cardCategory}>Articles Uploaded</p>
                        <h3 className={classes.cardTitle}>
                            {
                                data[1]
                            }
                        </h3>
                    </CardHeader>
                    <CardFooter stats>
                        <div className={classes.stats}>
                            <LocalOffer />
                            Articles we have uploaded
                         </div>
                    </CardFooter>
                </Card>
            </GridItem>
            <GridItem xs={12} sm={6} md={3}>
                <Card>
                    <CardHeader color="warning" stats icon>
                        <CardIcon color="warning">
                            <FaBullhorn />
                        </CardIcon>
                        <p className={classes.cardCategory}>Events uploaded</p>
                        <h3 className={classes.cardTitle}>
                            {
                               data[2]
                            }
                        </h3>
                    </CardHeader>
                    <CardFooter stats>
                        <div className={classes.stats} >
                            <Accessibility />
                            Events uploaded by users
                         </div>
                    </CardFooter>
                </Card>
            </GridItem>
            <GridItem xs={12} sm={6} md={3}>
                <Card>
                    <CardHeader color="danger" stats icon>
                        <CardIcon color="danger">
                            <FaGlobeAfrica />
                        </CardIcon>
                        <p className={classes.cardCategory}>News aired</p>
                        <h3 className={classes.cardTitle}>
                            {
                                data[3]
                            }
                        </h3>
                    </CardHeader>
                    <CardFooter stats>
                        <div className={classes.stats} >
                            <Satellite />
                            News aired through the system
                         </div>
                    </CardFooter>
                </Card>
            </GridItem>
        </GridContainer>
    )
    
};

export default General;