import React,{useState,useEffect} from 'react';
import {ScaleLoader} from 'react-spinners';

//core components
import {GridContainer,GridItem} from '../../Grid';
import View from './View';
import Pagination from './Pagination';
import {loadUsers} from '../../utils/data';

function Users() {

    
    const [users,setUsers] = useState([]); 
    const [loading,setLoading] = useState(true);
    const [pending] = useState(false);
    const [usersPerPage] = useState(3);
    const [currentPage,setCurrentPage] = useState(1);

    useEffect(() => {
        const fetchUsers = async () => {
            let users = await loadUsers().catch(console.log);
            setUsers(users);
            return setLoading(false);
        };
        fetchUsers();
    },[]);

    const Paginate = page => setCurrentPage(page);
    const indexOfLastUser = currentPage * usersPerPage;
    const indexOfFirstUser = indexOfLastUser - usersPerPage;
    const currentUsers = users.slice(indexOfFirstUser,indexOfLastUser);

    if(loading) return (
        <div className="centered_loader_component_holder">
            <div className="centered_component_loader">
                <ScaleLoader height="35" width="4" radius="2" margin="2px" color="#009933" />
            </div>
        </div>
    );

    return (
        <GridContainer>
            <GridItem xs={12} sm={12} md={12}>
                <View users={currentUsers} pending={pending}/>
            </GridItem>
            <GridItem xs={12} sm={12} md={12}>
                <Pagination usersPerPage={usersPerPage} current={currentPage} totalUsers={users.length} paginate={Paginate}/>
            </GridItem>                    
        </GridContainer>
    )
};


export default Users;