import React, { useState, useEffect } from 'react';
import Slick from 'react-slick';
import BlockHeader from './BlockHeader';

import AdvertCard from './AdvertCard.js';
import NewsCard from './NewsCard.js';
import ArticlesCard from './ArticlesCard.js';
import EventsCard from './EventCard';
import ProductsCard from './ProductCard';
import HostelsCard from './HostelCard';
import GalleryCard from './GalleryCard';

import { ClipLoader } from 'react-spinners';
import { FaAngleLeft, FaAngleRight } from 'react-icons/fa';

import { carouselSettings } from '../../data';;


//this file may need modifications

const Carousel = ({ rows, data, title, card, _id,see_more }) => {

    const [render, setRender] = useState(false);
    const [slickRef, setSlickRef] = useState('');


    useEffect(() => {
        async function updateRender() {
            return setRender(true);
        };
        updateRender();
    }, [render]);

    const handleNextClick = () => {
        if (slickRef) {
            return slickRef.slickNext();
        }
    };

    const handlePrevClick = () => {
        if (slickRef) {
            return slickRef.slickPrev();
        }
    };

    const dataColumns = () => {
        let dataToShow = [];
        let columns = [];
        if (rows > 0 && data.length > 0) {
            dataToShow = data.slice();
            while (dataToShow.length > 0) {
                columns.push(dataToShow.splice(0, rows));//get element one by one
            }
        }
        return columns;
    }


    if (data.length === 0) return (
        <div className="text-center my-20">
            <p>we shall display a banner for add message </p>
        </div>
    )
    let arrows = data.length > 3;
    let link;
    if(see_more){
        if (arrows && card === "events") link = '/admin/events';
        else if (arrows && card === "articles") link = `/admin/articles`;
        else if (arrows && card === "products") link = `/admin/mp`;
        else if (arrows && card === "news") link = `/admin/news`;
    };
    const columns = dataColumns().map((column, index) => {
        const content = column.map((data, index) => {
            if (card === "events") return <EventsCard key={index} data={data} />;
            else if (card === "articles") return <ArticlesCard key={index} data={data} />;
            else if (card === "products") return <ProductsCard key={index} data={data} />
            else if (card === "news") return <NewsCard key={index} data={data} />;
            else if (card === "hostels") return <HostelsCard key={index} data={data} />;
            else if (card === "gallery") return <GalleryCard key={index} data={data} _id={_id} />
            else return <AdvertCard key={index} data={data} />
        });
        return (
            <div key={index}>
                {content}
            </div>
        )
    });
    return (
        render ? (
            <div className="carousel_container">
                <div className="row">
                    <div className="header col-sm-12 col-12">
                        <BlockHeader
                            title={title}
                            link={link}
                        />
                        <div className="carousel_slider_container">
                            {
                                arrows && <div className="carousel_slider_btns">
                                    <button onClick={handlePrevClick} className="carousel_slider_btn--left">
                                        <FaAngleLeft />
                                    </button>
                                    <button onClick={handleNextClick} className="carousel_slider_btn--right">
                                        <FaAngleRight />
                                    </button>
                                </div>
                            }
                        </div>
                        {
                            <Slick ref={ref => setSlickRef(ref)}  {...carouselSettings}>
                                {columns}
                            </Slick>
                        }
                    </div>
                </div>
            </div>
        ) : (
                <div className="text-center my-20">
                    <ClipLoader size="25" sizeUnit="px" color="#009933"/>
                </div>
            )
    )

};

Carousel.defaultProps = {
    data: [],
    rows: 1,
    _id: null,
    see_more:true
}

export default Carousel;