import React,{useState,useEffect} from 'react';
import {ScaleLoader} from 'react-spinners';
import {makeStyles} from '@material-ui/core';
import {withRouter} from 'react-router-dom';

//core-components
import {Card,CardAvatar,CardBody} from '../../Card';
import {GridContainer,GridItem} from '../../Grid';
import styles from '../../../assets/jss/material-dashboard-react/views/dashboardStyle';
import avatar from '../../../assets/img/faces/marc.jpg';
import {loadSpecUser} from '../../utils/data';

const useStyles = makeStyles(styles);

const User = (props) => {

    const classes = useStyles();    
    const [loading,setLoading] = useState(true);
    const [user,setUser] = useState({});
    const {userId} = props.match.params;

    useEffect(() => {
        async function loadUser(){
            let {message} = await loadSpecUser(userId).catch(console.log);
            setUser(message);
            return setLoading(false);
           };
        loadUser();
    },[userId]);

    const blockUser = () => {
        /** call an api method for this */
        return alert('Waiting for aggreeement for blocking users')
    }

    if(loading) return (
        <div className="centered_loader_component_holder">
            <div className="centered_component_loader">
                <ScaleLoader height="35" width="4" radius="2" margin="2px" color="#009933" />
            </div>
        </div>
    );

    return (
        <GridContainer>
            <GridItem xs={12} sm={12} md={12}>
                <div className="user_container">
                    <h3 className="user_name">{user['firstname'] + ' ' + user['lastname'] + ' profile'}</h3>
                </div>
            </GridItem>
            <GridItem xs={12} sm={4} md={4}>
                <div className="user_container-content">
                    <Card profile>
                        <CardAvatar profile>
                            <img src={
                                user['profile'] ? user['profile'] : avatar
                            } alt={user['firstname']} />
                        </CardAvatar>
                        <CardBody profile>
                            {
                                user['firstname'] && <h6 className={classes.cardCategory}>{user['firstname'] + ' ' + user['lastname']}</h6>
                            }
                            {
                                user['email'] && <h5 className="user_contacts">Contact: {user['email']}</h5>
                            }
                            {
                                user['phoneNumber'] && <h5 className="user_contacts">Phone: {user['phoneNumber']}</h5>
                            }
                            <p className="text-muted">{user['status']}</p>
                        </CardBody>
                        
                        <div className="user_container-action">
                            <button className="btn btn-danger" onClick={() => blockUser(user['_id'])}>Block user</button>
                        </div>                            
                        
                    </Card>
                </div>
            </GridItem>
        </GridContainer>
    )
};

export default withRouter(User);