import React from 'react';
import {confirmAlert} from 'react-confirm-alert';
import {Link} from 'react-router-dom';

//core-components
import 'react-confirm-alert/src/react-confirm-alert.css';
import {delete_advert} from '../../store/data';

const AdvertCard = ({data:advert}) => {
    
    const handleDelete = async (advert_id) => {
       confirmAlert({
           title:'Confirm to delete advert',
           message:'Are you sure you want to delete this advert',
           buttons:[
               {
                   label:'Yes',
                   onClick: async () => {
                       await delete_advert(advert_id).catch(console.log);
                       return document.location.reload();
                   }
               },{
                   label:'No',
                   onClick:async () => {
                       return document.location.reload();
                   }
               }
           ]
       })
    };

    return (
        <div className="card adverts_card">
            <img src={`${advert.advert}`} className="card-img-top advert_card_img" alt={advert.btnInfo} />
            <div className="card-body">
                <div className="media">
                    <div className="media-body">
                        <h4 className="card_heading"><span><b>description</b></span>: {advert.description}</h4>
                        <p className="media-text card_text"><span><b>btn Info</b></span>: {advert.btnInfo}</p>
                        <button className="btn btn-sm btn-danger" onClick={() => handleDelete(advert._id)}>delete advert</button>
                        {' '}
                        <Link className="btn btn-sm btn-success" to={{
                            pathname:'/admin/advertForm',
                            state:{
                                advert
                            }
                        }}>edit details</Link>
                    </div>
                </div>
            </div>
        </div>
    )
};

export default AdvertCard;