import React,{Component} from 'react';
import {Link} from 'react-router-dom';
//core-components
import {GridItem} from '../../Grid';
import {addAdvert,updateAdvert} from '../../utils/data';

class  AdvertForm extends Component{

    state = {
        link:'',
        update:false,
        view_adverts:false,
        advert_id:'',
        error:'',
        mobile_advert:'',
        advert_description:'',
        advert_division:'events',
        btn_info:'',
        advert_type:'slider',
        tagged:false,
        desktop_advert:'',
        action:'upload advert'
    };
   
   /** we can have an edit for advert */
   componentDidMount(){
    
      if(this.props.location.state){
          /** we are not going to load the images fields */
          let {advert} = this.props.location.state;
          let {link,description,division,btnInfo,type,tagged,_id} = advert;
          return this.setState({
              link,advert_description:description,advert_division:division,btn_info:btnInfo,advert_id:_id,advert_type:type,tagged,update:true
          });

      };
   };    
   onChange = e => {


      if(e.target.name === 'mobile_advert' || e.target.name  === 'desktop_advert'){
          return this.setState({
              [e.target.name] : e.target.files[0]
          })
      };

      /** for tagged */
      if(e.target.name === "tagged") return this.setState({tagged:!this.state.tagged});
      /** for divisons when they change */

      if(e.target.name === 'advert_division'){

          if(e.target.value === 'market_place') return this.setState({
              'link':'app/dashboard/market_place/add_product',
              tagged:true
          });
          else if(e.target.value === 'events') return this.setState({
              'link':'app/dashboard/upcoming_events/add_event',
              tagged:true
          });
          else if(e.target.value === 'articles') return this.setState({
              'link':'app/content/articles'
          });

      }
      return this.setState({
          [e.target.name] : e.target.value
      })
   };

   onSubmit = async e => {

        e.preventDefault();
        let {link,mobile_advert,desktop_advert,advert_division,
              advert_description,advert_type,btn_info,tagged,update,advert_id} = this.state;

        this.setState({error:''});
        const formData = new FormData();
        const data = {};
        if(update){

            
            if ( !advert_division || !link  || !advert_description || !advert_type || !btn_info) return this.setState({
                    error: 'Fill all the fields'
            });

            data['link'] = link;data['division'] = advert_division;data['description'] = advert_description;
            data['btnInfo'] = btn_info;data['tagged'] = tagged;data['type'] = advert_type;
            this.setState({ action: 'uploading' });
            let {success,message} = await updateAdvert(data,advert_id).catch(console.log);
            if(!success) return this.setState({error:message});
            console.log('message',message);
            return this.setState({error: '', tagged: message.tagged, link: message.link, advert_description: message.description,
                advert_division: message.division,btn_info: message.btnInfo, advert_type: message.type, 
                action: 'updated', view_adverts: true});
            
            
        }else{

            if (!mobile_advert || !advert_division || !link || !desktop_advert || !advert_description
                || !advert_type || !btn_info) return this.setState({
                    error: 'Fill all the fields'
            });

            formData.append('link', link); formData.append('adverts', desktop_advert); formData.append('adverts', mobile_advert);
            formData.append('division', advert_division); formData.append('description', advert_description);
            formData.append('type', advert_type); formData.append('btnInfo', btn_info); formData.append('tagged', tagged);
            this.setState({ action: 'uploading' });
            let {success,message} = await addAdvert(formData).catch(console.log);
            if (!success) return this.setState({ error: message });
            return this.setState({
                error: '', link: '', advert_description: '', advert_division: '', btn_info: '', advert_type: '', action: 'uploaded',
                view_adverts: true
            });
        }
        
    };
    render(){

    const advert_types = ['home','slider'];
    const advert_divisions = ['market_place','events','articles'];
    const {error,update,view_adverts} = this.state;

    return (
        <>
        <GridItem xs={12} sm={12} md={12}>
            <div className="adverts_card">
            <h3 className="adverts_card--heading">
                {
                    update ? 'Update an event' : 'Add an event'
                }
            </h3>
            <form onSubmit={this.onSubmit}>
                {
                    error && <p className="text-danger">{error}</p>
                }
                <div className="form-group">
                    <label htmlFor='advertDivision'>Advert Division</label>
                    <select className="form-control" name="advert_division" onChange={this.onChange}>                        
                        <option value={this.state.advert_division}>{this.state.advert_division}</option>
                        {
                            advert_divisions.filter(division => division !== this.state.advert_division)
                            .map((division,key) => <option key={key} value={division}>{division}</option>)
                        }
                    </select>
                </div>
                {
                    
                !update && (
                    <>
                        <div className="form-group">
                            <label htmlFor='advert'>mobile Advert</label>
                            <input type="file"
                                className="form-control"
                                name="mobile_advert"
                                id="advert"
                                onChange={this.onChange}
                            />
                        </div>
                        <div className="form-group">
                            <label htmlFor='advert'>desktop advert</label>
                            <input type="file"
                                className="form-control"
                                name="desktop_advert"
                                id="advert"
                                onChange={this.onChange}
                            />
                        </div>
                    </>
                ) 
                }
                
                <div className="form-group">
                    <label htmlFor='description'>advert description</label>
                    <textarea className='form-control' 
                     rows='5' cols='40'
                     placeholder='Enter a five word summarized title'
                     onChange={this.onChange} 
                     name='advert_description' 
                     value={this.state.advert_description}
                     />
                </div>
                <div className="form-group">
                    <label htmlFor="link">Link</label>
                    <p className="text-success">Link is updated once you choose a division</p>
                    <input type="text"
                        placeholder="link to the application"
                        className="form-control"
                        value={this.state.link}
                        readOnly
                        name="link"
                        id="link"
                    />
                </div>
                <div className="form-group">
                    <label htmlFor="advert_type">Advert type</label>
                    <select name="advert_type" className="form-control" onChange={this.onChange}>
                        <option value={`${this.state.advert_type}`}>{this.state.advert_type}</option>
                        {
                            advert_types.filter(advert => advert !== this.state.advert_type).map((advert, key) => (
                                <option key={key} value={advert}>{advert}</option>
                            ))
                        }
                    </select>
                </div>
                <div className="form-group">
                    <label htmlFor="tagged" className="tagged">Tagged</label>
                    <input type="checkbox"
                    checked={this.state.tagged}
                    name="tagged"
                    onChange={this.onChange}
                    id="tagged"
                    className="align-left"
                    style={{marginLeft:"20px"}}
                    />
                </div>
                <div className="form-group">
                    <label htmlFor="btn_info">Button Info</label>
                    <input type="text"
                    name="btn_info"
                    placeholder="What should the button display"
                    className="form-control"
                    value={this.state.btn_info}
                    onChange={this.onChange}
                    />                    
                </div>    
                <div className="form-group">
                    <input type="submit" className="btn btn-outline-success form_btn" value={this.state.action} />
                    {
                      view_adverts && <Link to={`/admin/adverts`} className="btn btn-outline-success form_btn">view adverts</Link>   
                    }
                </div>
            </form>
         </div>
        </GridItem>
    </>
)
}};

export default AdvertForm;