import React,{useState,useEffect} from 'react';
import {ScaleLoader} from 'react-spinners';
//core-components

import Banner from '../../shared/Banner';
import BlockHeader from '../../shared/BlockHeader';
import ProductCard from '../../shared/ProductCard';
import Carousel from '../../shared/Carousel';
import {food_vegs_products,clothing_products,electronics_products,furniture_products} from '../../../store/data';

const Mp = () => {

    const [food_vegs,setFoodVegs] = useState([]);
    const [clothing,setClothing] = useState([]);
    const [electronics,setElectronics] = useState([]);
    const [furniture,setFurniture] = useState([]);
    const [loading,setloading] = useState(true);
    let food_vegs_to_show = '';
    let clothing_to_show = '';
    let furniture_to_show = '';
    let electronics_to_show = '';

    useEffect(() => {
        async function loadProducts(){

            let _food_vegs = await food_vegs_products().catch(console.log);
            let _clothing = await clothing_products().catch(console.log);
            let _electronics = await electronics_products().catch(console.log);
            let _furniture = await furniture_products().catch(console.log);

            setFoodVegs(_food_vegs);setClothing(_clothing);setElectronics(_electronics);setFurniture(_furniture);

            return setloading(false);
        };
        loadProducts();
    },[]);

    if(loading) return (
        <div className="centered_loader_component_holder">
            <div className="centered_component_loader">
                <ScaleLoader height="35" width="4" radius="2" margin="2px" color="#009933" />
            </div>
        </div>
    );

    if(food_vegs.length === 0 && clothing.length === 0 && electronics.length === 0 && furniture.length === 0) return (
         <Banner message={`Sorry no products have been uploaded yet`} />
    );
    
    if (food_vegs.length >= 3) {
        food_vegs_to_show = <Carousel data={food_vegs} card="products" title="Food and Vegetables" />
    } else if (food_vegs.length > 0 && food_vegs.length < 3) {
        food_vegs_to_show = <div className="row">
            <div className="col-12 col-sm-12"> <BlockHeader title="Food and Vegetables" /> </div>
            {
                food_vegs.map((product, index) => (
                    <div className="col-12 col-sm-4 col-md-4" key={index}>
                        <ProductCard data={product} />
                    </div>
                ))
            }
        </div>
    };

    if (clothing.length >= 3) {
        clothing_to_show = <Carousel data={clothing} card="products" title="Clothing" />
    } else if (clothing.length > 0 && clothing.length < 3) {
        clothing_to_show = <div className="row">
            <div className="col-12 col-sm-12"> <BlockHeader title="Clothing" /> </div>
            {
                clothing.map((product, index) => (
                    <div className="col-12 col-sm-4 col-md-4" key={index}>
                        <ProductCard data={product} />
                    </div>
                ))
            }
        </div>
    };

    if (electronics.length >= 3) {
        electronics_to_show = <Carousel data={electronics} card="products" title="Electronics" />
    } else if (electronics.length > 0 && electronics.length < 3) {
        electronics_to_show = <div className="row">
            <div className="col-12 col-sm-12"> <BlockHeader title="Electronics" /> </div>
            {
                electronics.map((product, index) => (
                    <div className="col-12 col-sm-4 col-md-4" key={index}>
                        <ProductCard data={product} />
                    </div>
                ))
            }
        </div>
    };

    if (furniture.length >= 3) {
        furniture_to_show= <Carousel data={furniture} card="products" title="Furniture" />
    } else if (furniture.length > 0 && furniture.length < 3) {
        furniture_to_show = <div className="row">
            <div className="col-12 col-sm-12"> <BlockHeader title="Furniture" /> </div>
            {
                furniture.map((product, index) => (
                    <div className="col-12 col-sm-4 col-md-4" key={index}>
                        <ProductCard data={product} />
                    </div>
                ))
            }
        </div>
    };

    return (
        <React.Fragment>
            {
               food_vegs_to_show
            }
            {
               clothing_to_show
            }
            {
               electronics_to_show
            }
            {
               furniture_to_show
            }
        </React.Fragment>
    
    )
};

export default Mp;