import React from "react";
import { Switch, Route,Redirect} from "react-router-dom";
import PerfectScrollbar from "perfect-scrollbar";
import {ClipLoader} from 'react-spinners';
import "perfect-scrollbar/css/perfect-scrollbar.css";

import { makeStyles } from "@material-ui/core/styles";
import Navbar from "../components/Navbars/Navbar";
import Footer from "../components/Footer/Footer";
import Sidebar from "../components/Sidebar/Sidebar";

import routes from "../routes";
import styles from "../assets/jss/material-dashboard-react/layouts/adminStyle";
import bgImage from "../assets/img/sidebar-2.jpg";
import logo from "../assets/img/brand.png";
import Event from "../components/views/Events/Event";
import User from "../components/views/Users/User";
import Product from "../components/views/Mp/Product";
import Feedbacks from "../components/views/Feedbacks";
import AdvertForm from "../components/views/Adverts/AdvertForm";
import HostelPage from '../components/views/Hostels/HostelPage';
// import HostelForm from "../components/views/Hostels/HostelForm";
import HostelForm from "../components/views/Hostels/HostelForm";
import HostelImages from "../components/views/Hostels/HostelImages";

let ps;
const useStyles = makeStyles(styles);
function Admin() {
  // styles
  const classes = useStyles();
  const mainPanel = React.createRef();
  const [token] = React.useState(localStorage.getItem('admin_token'));
  const [render,setRender] = React.useState(false);
  const [image] = React.useState(bgImage);
  const [color] = React.useState("blue");
  const [mobileOpen, setMobileOpen] = React.useState(false);
  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };
  const getRoute = () => {
    return window.location.pathname !== "/admin/maps";
  };
  const resizeFunction = () => {
    if (window.innerWidth >= 960) {
      setMobileOpen(false);
    }
  };
  React.useEffect(() => {
    if (navigator.platform.indexOf("Win") > -1) {
      ps = new PerfectScrollbar(mainPanel.current, {
        suppressScrollX: true,
        suppressScrollY: false
      });
      document.body.style.overflow = "hidden";
    }
    window.addEventListener("resize", resizeFunction);
    function cleanup() {
      if (navigator.platform.indexOf("Win") > -1) {
        ps.destroy();
      }
      window.removeEventListener("resize", resizeFunction);
    };
    setRender(true);
    cleanup();
  }, [mainPanel]);

  if(!token) return <Redirect to="/admin/login" />

  if(!render) return (
    <div className="text-center">
      <ClipLoader size="25" sizeUnit="px" color="#009933" />
    </div>
  )

  const switchRoutes = (
    <Switch>
      {routes.map((prop, key) => {
        if (prop.layout === "/admin") {
          return (
            <Route
              exact
              path={prop.layout + prop.path}
              component={prop.component}
              key={key}
            />
          );
        }
        return null;
      })}
      <Route path="/admin/event/:eventId" component={Event} />
      <Route path="/admin/user/:userId" component={User} />
      <Route path="/admin/product/:prodId" component={Product} />
      <Route path="/admin/advertForm" component={AdvertForm} />
      <Route path="/admin/feedbacks" component={Feedbacks} />
      <Route exact path="/admin/hostel_form" component={HostelForm} />

      <Route exact path="/admin/hostel_form/edit_images" component={HostelImages} />
      
      <Route path="/admin/hostels/:hostelId" component={HostelPage} /> 
      <Redirect from="/" to="/admin/dashboard" />
    </Switch>
  );

  return (
    <div className={classes.wrapper}>
      <Sidebar
        routes={routes}
        logo={logo}
        image={image}
        handleDrawerToggle={handleDrawerToggle}
        open={mobileOpen}
        color={color}
      />
      <div className={classes.mainPanel} ref={mainPanel}>
        <Navbar
          routes={routes}
          handleDrawerToggle={handleDrawerToggle}
        />
        {/* On the /maps route we want the map to be on full screen - this is not possible if the content and conatiner classes are present because they have some paddings which would make the map smaller */}
        {getRoute() ? (
          <div className={classes.content}>
            <div className={classes.container}>{switchRoutes}</div>
          </div>
        ) : (
          <div className={classes.map}>{switchRoutes}</div>
        )}
        {getRoute() ? <Footer /> : null}
       
      </div>
    </div>
  );
}



export default Admin;