import React from 'react';
import axios from 'axios';
import { ClipLoader } from 'react-spinners';
import {Redirect} from 'react-router-dom';

//core-components
import Nav from './Nav';
import login_image from '../images/login_image.jpg';

class Login extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            redirect:false,
            error: '',
            password: "",
            confirm_pass:'',
            userId:this.props.match.params.userId,
            loading: false,
            action: 'save password'
        }
    };
    onChange = (e) => {
        return this.setState({
            [e.target.name]: e.target.value
        })
    };
    onSubmit = async  e => {
        e.preventDefault();
        this.setState({ error: '' });
        if (!this.state.password || !this.state.confirm_pass)return this.setState({
            error:'Fill out the fields'
        });
        if(this.state.password !== this.state.confirm_pass) return this.setState({
            error:'Passwords Mismatch'
        });
        if(this.state.password.length < 6) return this.setState({
            error:'Minimum six characters'
        });
        const data = { password:this.state.password,userId:this.state.userId };
        this.setState({ action: "resetting", loading: true });
        let res = await axios.post(
            'https://karuapi.herokuapp.com/api/admin/setPassword'
            , data, {
                headers: { 'Content-Type': 'application/json' }
            }).catch(console.log);
        if (!res.data.success) return this.setState({ error: res.data.message, loading: false, action: "Try Again" });
        if (res.data.success) {
            this.setState({ error: '', email: '', action: 'set', loading: false, redirect:true});
        }
    };
    render() {
        const {redirect} = this.state;
        if(redirect) return <Redirect to="/" />
        return (
            <section>
                <Nav btn="set password" />
                <div className="auth_container container">
                    <div className="row">
                        <div className="d-none d-sm-block d-lg-block col-sm-6 col-lg-6">
                            <div className="auth_image_container" style={{ backgroundImage: `url(${login_image})` }}>
                                <div className="auth_image_inner">
                                    <div className="auth_content">
                                        <h3 className="auth_content_heading">allComrades Admin Dashboard</h3>
                                        <p className="auth_content_text">set your password </p>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="col-sm-6 col-12 col-md-6 col-lg-6">
                            <form onSubmit={this.onSubmit}>
                                {
                                    this.state.error && <p className="text-danger">
                                        {this.state.error}
                                    </p>
                                }
                                <div className="form-group">
                                    <label htmlFor="password">Password</label>
                                    <input type="password"
                                        className="form-control"
                                        name="password"
                                        value={this.state.password}
                                        placeholder="Enter your new Password"
                                        onChange={this.onChange}
                                    />
                                </div>
                                <div className="form-group">
                                    <label htmlFor="conform">Confirm Password</label>
                                    <input type="password"
                                        className="form-control"
                                        name="confirm_pass"
                                        value={this.state.confirm_pass}
                                        placeholder="Confirm your Password"
                                        onChange={this.onChange}
                                    />
                                </div>
                                <div className="form-group">
                                    {
                                        this.state.loading ? <ClipLoader size='20' sizeUnit="px" /> : (
                                            <input type="submit" value={this.state.action} className='btn btn-outline-success' />
                                        )
                                    }
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </section>
        )
    }
};

export default Login;