import React from 'react';
import axios from 'axios';
import {ClipLoader} from 'react-spinners';

//core-components
import Nav from './Nav';
import login_image from '../images/login_image.jpg';

class Login extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            info:'',
            error: '',
            email: "",
            loading:false,
            action: 'reset password'
        }
    };


    onChange = (e) => {
        return this.setState({
            [e.target.name]: e.target.value
        })
    };
    onSubmit = async  e => {
        e.preventDefault();
        this.setState({ error: '' });
        if (!this.state.email) {
            return this.setState({
                error: "You must enter your email address"
            })
        };
        const data = { email: this.state.email};
        this.setState({ action: "sending email",loading:true });
        let res = await axios.post(
            'https://karuapi.herokuapp.com/api/admin/recover_password'
            , data, {
                headers: { 'Content-Type': 'application/json' }
            }).catch(console.log);
        if (!res.data.success) return this.setState({ error: res.data.message,loading:false,action: "Try Again" });
        if (res.data.success) {
            this.setState({ error: '', email: '', action: 'recover password',loading:false,info: res.data.message })
        };
    };
    render() {
        return (
            <section>
                <Nav btn="recover password" />
                <div className="auth_container container">
                    <div className="row">
                        <div className="d-none d-sm-block d-lg-block col-sm-6 col-lg-6">
                            <div className="auth_image_container" style={{ backgroundImage: `url(${login_image})` }}>
                                <div className="auth_image_inner">
                                    <div className="auth_content">
                                        <h3 className="auth_content_heading">allComrades Admin Dashboard</h3>
                                        <p className="auth_content_text">reset your password</p>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="col-sm-6 col-12 col-md-6 col-lg-6">
                            <form onSubmit={this.onSubmit}>
                                {
                                    this.state.error && <p className="text-danger">
                                        {this.state.error}
                                    </p>
                                }
                                <div className="form-group">
                                    <label htmlFor="email">Email</label>
                                    <input type="email"
                                        className="form-control"
                                        name="email"
                                        value={this.state.email}
                                        placeholder="Enter your account email"
                                        onChange={this.onChange}
                                    />
                                </div>
                                <div className="form-group">
                                    {
                                        this.state.loading ? <ClipLoader size='20' sizeUnit="px" /> : (
                                            <input type="submit" value={this.state.action} className='btn btn-outline-success' />
                                        )                                        
                                    }                                    
                                </div>
                                {
                                    this.state.info && <p className="text-success">
                                        {this.state.info}
                                    </p>
                                }
                            </form>
                        </div>
                    </div>
                </div>
            </section>
        )
    }
};

export default Login;