import React,{useState,useEffect} from 'react';
import axios from 'axios';
import {ClipLoader} from 'react-spinners';
import { Table, TableHead, TableBody, TableCell, Paper, makeStyles, TableRow } from '@material-ui/core';
import {Modal,ModalHeader,ModalBody} from 'reactstrap';
//core components
import { GridContainer, GridItem } from '../../Grid';
import { Card, CardHeader, CardBody } from '../../Card';

const useStyles = makeStyles(theme => ({
    root: {
        width: '100%',
        marginTop: theme.spacing(3),
        overflowX: 'auto',
    },
    table: {
        minWidth: 650,
    },
}));



const Views = ({feedbacks,deleteFd}) => {
    const classes = useStyles();

    const [admin,setAdmin] = useState({}); 
    const [token] = useState(localStorage.getItem('admin_token'));
    const [message,setMessage] = useState('');
    const [loading,setLoading] = useState(false);
    const [_id,setId] = useState('');
    const [error,setError] = useState('');
    const [action,setAction] = useState('send response');
    const [modal,setModal] = useState(false);
    
    useEffect(() => {
        const loadAdmin = async () => {
        let res = await axios.post(
                `https://karuapi.herokuapp.com/api/admin/decodeToken`,null,{
                    'headers':{'Authorization':`Bearer ${token}`}
                }
            ).catch(console.log);
            if(res.data.success) return setAdmin(res.data.message)
        };
        loadAdmin();
    },[token]);

    const messageOnchange = e => {
        return setMessage(e.target.value);
    };

    const toggle = () => {
        return setModal(!modal);
    };

    const sendResponse = (_id) => {
        setModal(!modal);
        return setId(_id);
    };

    const submit = async e => {
        e.preventDefault();
        if(!message) return setError('Enter the response you want to send');
        const feedback = {fb_id:_id,message:message,admin_id:admin['_id']}; 
        setLoading(true);
        let res = await axios.post(
            `https://karuapi.herokuapp.com/api/feedbacks/sendResponse`,
            feedback,{
            headers:{'Content-Type':'application/json'}
            }).catch(console.log);
        if(!res.data.success){
            setLoading(false);
            return setError(res['data']['message']);
        } else {
            setMessage('');setError('');setAction('sent');
            return setModal(false)
        };
        
    };

    return (
        <GridContainer>
            <GridItem xs={12} sm={12} md={12}>
                <Modal isOpen={modal} toggle={toggle}>
                <ModalHeader toggle={toggle}>
                    send a Feedback response
                </ModalHeader>
                <ModalBody>
                <form onSubmit={submit}>
                    {
                        error && <p className="text-danger">
                        {error}
                        </p>
                    }
                    <div className="form-group">
                        <label htmlFor="message">Send a Message</label>
                        <textarea name="message"
                        id="response"
                        className="form-control"
                        placeholder="Enter response to send"
                        rows="5"
                        cols="30"
                        onChange={messageOnchange}
                        />
                    </div>
                    <div className="form-group">
                    {
                        loading ? <ClipLoader size="20" sizeUnit="px" /> :
                        (
                            <input type="submit" className="btn btn-outline-success" value={action} />
                        )
                    }                        
                    </div>
                </form>
                </ModalBody>
                </Modal>
                <Card>
                    <CardHeader color="success">
                        <h3 className="card_header--heading">Received Feedbacks</h3>
                    </CardHeader>
                        <CardBody>
                         <Paper className={classes.root}>
                            <Table className={classes.table}>
                            <TableHead>
                                <TableRow>
                                    {
                                        ['email', 'subject', 'message', 'date posted','action'].map((cell, index) => {
                                            return (
                                                <TableCell key={index}>{cell}</TableCell>
                                            )
                                        })
                                    }
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                {
                                    feedbacks.map((fd, index) => {
                                        return (
                                            <TableRow key={index}>
                                                <TableCell>{fd['email']}</TableCell>
                                                <TableCell>{fd['subject']}</TableCell>
                                                <TableCell>{fd['message']}</TableCell>
                                                <TableCell>{new Date(fd['createdAt']).toDateString()}</TableCell>
                                                <TableCell>
                                                    <div className="btn-group">
                                                        <button className="btn btn-outline-success" onClick={() => sendResponse(fd['_id'])}>respond</button>
                                                        <button className="btn btn-outline-danger" onClick={() => {deleteFd(fd['_id'])}}>delete</button>
                                                    </div>                                                                
                                                </TableCell>
                                            </TableRow>
                                        )
                                    })
                                }
                            </TableBody>
                            </Table>
                        </Paper>
                    </CardBody>
                </Card>
            </GridItem>
        </GridContainer>
    )};


export default Views;