import {ADVERTS_PENDING,MOBILE_ADVERTS_FETCH,DESKTOP_ADVERTS_FETCH,
        MOBILE_ADVERTS_ADD,DESKTOP_ADVERTS_ADD,MOBILE_ADVERTS_REMOVE,DESKTOP_ADVERTS_REMOVE} from './types';

const initialState = {
    pending:false,
    mobile_adverts:[],
    desktop_adverts:[]
};

export function advertsReducer(state=initialState,action){
    switch(action.type){
        case ADVERTS_PENDING:
            return {
                ...state,
                pending:true
            }
        case MOBILE_ADVERTS_FETCH:
            return {
                ...state,
                pending:false,
                mobile_adverts:action.payload
            }
        case DESKTOP_ADVERTS_FETCH:
            return {
                ...state,
                pending:false,
                desktop_adverts:action.payload
            }
        case MOBILE_ADVERTS_ADD:
            return {
                ...state,
                pending:false,
                mobile_adverts:[...state.mobile_adverts,action.payload]
            }
        case DESKTOP_ADVERTS_ADD:
            return {
                ...state,
                pending: false,
                desktop_adverts: [...state.desktop_adverts, action.payload]
            }
        case MOBILE_ADVERTS_REMOVE:
            return {
                ...state,
                pending:false,
                mobile_adverts:[...state.mobile_adverts.filter(advert => advert._id !== action.payload)]
            }
        case DESKTOP_ADVERTS_REMOVE:
            return {
                ...state,
                pending:false,
                desktop_adverts:[...state.desktop_adverts.filter(advert => advert._id !== action.payload)]
            }
        default:
            return {
                ...state
            }
    }
};
export const getAdvertsData = state => state.adverts;