import {USERS_PENDING,USERS_SUCCESS} from './types';

const initialState = {
    pending:false,
    users:[]
};

export function usersReducer(state=initialState,action){
    switch(action.type){
        case USERS_PENDING:
            return {
                ...state,
                pending:true
            }
        case USERS_SUCCESS:
            return {
                ...state,
                pending:false,
                users:action.payload
            }
        default:
            return {
                ...state
            }
    }
};

export const getUsersData = state => state.users;