import React,{useState,useEffect} from 'react';
import {ScaleLoader} from 'react-spinners';

//core-components
import General from './General';
import {upcoming_events,fetch_articles} from '../../../store/data';
import Carousel from '../../shared/Carousel';


const Dashboard = () => {
   
  const [events,setEvents] = useState([]);
  const [articles,setArticles] = useState([]);
  const [loading,setLoading] = useState(true);

  useEffect(() => {
    const load_data = async () => {
        let _events = await upcoming_events().catch(console.log);
        let _articles = await fetch_articles().catch(console.log);
        setEvents(_events);setArticles(_articles);
        return setLoading(false);
    };
    load_data();
  });

  if(loading) return (
    <div className="centered_loader_component_holder">
      <div className="centered_component_loader">
        <ScaleLoader height="35" width="4" radius="2" margin="2px" color="#009933" />
      </div>
    </div>
  );

  return (
    <>
    <General />
    {
       events.length > 0 && <Carousel title={'Upcoming events'} data={events} card={'events'} />
    }
    {
      articles.length > 0 && <Carousel title={'Recent articles'} data={articles} card={'articles'} />
    }
    </>
  )
};

export default Dashboard;