import {hostelPending,hostelSuccess,deleteHostelSuccess} from './actions';
import axios from 'axios';

const URL = `https://karuapi.herokuapp.com/api/`;

export const fetchHostels = () => {
    return dispatch => {
        dispatch(hostelPending());
        axios.get(`${URL}/hostels/fetchHostels`).then(
            result => {
                return dispatch(hostelSuccess(result.data.message));                
            }
        ).catch(console.log);
    };    
};

export const deleteHostel = (_id,cb = () => console.log(`deleting hostel`)) => {
    return async dispatch => {
        dispatch(hostelPending());
        axios.delete(`${URL}/hostels/deleteHostel/${_id}`).then(result => {
            return dispatch(deleteHostelSuccess(_id));
        }).catch(console.log);
    };    
}