import React from 'react';
import {Table, TableHead, TableBody, TableCell, Paper, makeStyles, TableRow} from '@material-ui/core';

//core-components
import {Card,CardHeader, CardBody} from '../../Card';

const useStyles = makeStyles(theme => ({
    root: {
        width: '100%',
        marginTop: theme.spacing(3),
        overflowX: 'auto',
    },
    table: {
        minWidth: 650,
    },
}));

const Views = ({notifs,deleteNotif}) => {
    const classes = useStyles();
    return (
        <Card>
            <CardHeader color="primary">
                <h3 className="card_header--heading text-white">Received Notifications</h3>
            </CardHeader>
            <CardBody>
                <Paper className={classes.root}> 
                    <Table className={classes.table}>
                        <TableHead>
                            <TableRow>
                                {
                                    ['message','createdAt','action'].map((notif,index) => (
                                        <TableCell key={index}>{notif}</TableCell>
                                    ))
                                }
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {
                            notifs.map((notif, index) => {
                                return (
                                    <TableRow key={index}>
                                        <TableCell>{notif['message']}</TableCell>
                                        <TableCell>{new Date(notif['postedAt']).toDateString()}</TableCell>
                                        <TableCell>
                                            <button className="btn btn-danger" onClick={() => deleteNotif(notif['_id'])}>
                                                delete
                                            </button>
                                        </TableCell>
                                    </TableRow>
                                )
                            })}
                        </TableBody>
                    </Table>
                </Paper>
            </CardBody>
        </Card>
    )
};


export default Views;