import React,{useState,useEffect} from 'react';
import {ScaleLoader} from 'react-spinners';
//core-components

import Pagination from './Pagination';
import View from './View';
import {fetchFeedbacks,deleteFeedback} from '../../utils/data';

const Feedbacks = () => {

    const [feedbacks,setFeedbacks] = useState([]);
    const [loading,setLoading] = useState(true);
    const [modal] = useState(false);
    const [currentPage,setCurrentPage] = useState(1);
    const [fdsPerPage] = useState(5);
    const totalFds = feedbacks.length;

    useEffect(() => {
        const loadFeedbacks = async () => {
            const {message} = await fetchFeedbacks().catch(console.log);
            setFeedbacks(message);
            return setLoading(false);
        };
        loadFeedbacks();
    },[]);

    const paginate = number => setCurrentPage(number);

    const deleteFd = async _id => {
        setLoading(true);
        let {success} = await deleteFeedback(_id).catch(console.log);
        if(success){
            let _fds = await fetchFeedbacks().catch(console.log);
            setFeedbacks(_fds);
            return setLoading(false);
        };
        return setLoading(false);
    };

    const indexOfLastPost = currentPage * fdsPerPage;
    const indexOfFirstPost = indexOfLastPost - fdsPerPage;
    const currentFds = feedbacks.slice(indexOfFirstPost,indexOfLastPost);

    if(loading) return (
        <div className="centered_loader_component_holder">
            <div className="centered_component_loader">
                <ScaleLoader height="35" width="4" radius="2" margin="2px" color="#009933" />
            </div>
        </div>
    );
    
    

    return (
        <section>
            <div className="row">
                <div className="col-12 col-sm-12 col-md-12">
                    <View feedbacks={currentFds} modal={modal} deleteFd={deleteFd} />
                    <Pagination fdsPerPage={fdsPerPage} totalFds={totalFds} paginate={paginate}/>
                </div>
            </div>
        </section>
    )
};


export default Feedbacks;