
//Fast access to API'S other than redux store we have this

import axios from 'axios';
const URL = `https://karuapi.herokuapp.com/api/`;

export const verifyToken = async (token) => {

    let res = await axios.post(`${URL}/admin/verifyToken`,null,{
        headers:{
            'Authorization':`Bearer ${token}`
        }
    }).catch(console.log);
    return res.data.success;

};
export const decodeToken = async (token) => {

    let res = await axios.post(`${URL}/admin/decodeToken`, null, {
        headers: {
            'Authorization': `Bearer ${token}`
        }
    }).catch(console.log);
    return {
        success:res.data.success,
        message:res.data.message
    };

};

//updating an advert
export const updateAdvert = async (data,advert_id) => {
    
    let res = await axios.put(`${URL}/adverts/updateAdvert/${advert_id}`,data,{
        headers:{'Content-Type':'application/json'}
    }).catch(console.log);
    console.log(`what was returned `,res.data.message);
    return {
        success:res.data.success,
        message:res.data.message
    }

};

//adding an advert
export const addAdvert = async (data) => {

    let res = await axios.post(`${URL}/adverts/addAdvert`,data,{
        headers:{'Content-Type':'multipart/form-data'}
    }).catch(console.log);   
    return {
        success:res.data.success,
        message:res.data.message
    };
    
};

//load admins notifications
export const loadNotifs = async (token) => {
    let message = await decodeToken(token).catch(console.log);
    let {_id} = message.message;
    let _notifs = await axios.get(`${URL}/notifications/fetchNotifications`).catch(console.log);
    let admin_notifs = _notifs.data.message.filter(notif =>  notif.receiver.toString() !== _id.toString());
    
    return admin_notifs;
};

//delete notif
export const deleteNotif = async (_id) => {
    let res = await axios.delete(`${URL}/notifications/deleteNotification/${_id}`).catch(console.log);
    return {
       success:res.data.success,
       message:res.data.message
    }
};

