import React,{useState,useEffect} from 'react';
import {ClipLoader} from 'react-spinners';
import Slick from 'react-slick';
import {withRouter} from 'react-router-dom';
import {Link} from 'react-router-dom';
//core-components
import {GridContainer,GridItem} from '../../Grid';
import avatar from '../../../assets/img/faces/marc.jpg';
import {fetch_related_events,fetch_event} from '../../../store/data';
import Carousel from '../../shared/Carousel';
import {carouselSettings as slickSettings} from '../../../data';

const Event = (props) => {

    const [loading,setLoading] = useState(true);
    const [event,setEvent] = useState({});
    const [related,setRelated] = useState([]);
    const {eventId} = props.match.params;

    useEffect(() => {
        async function loadData(){
            let _event = await fetch_event(eventId).catch(console.log);
            let _related = await fetch_related_events(eventId).catch(console.log);
            
            setEvent(_event);setRelated(_related);
            return setLoading(false);            
        };
        loadData();
    },[eventId]);

    if(loading) return (
        <div className="text-center my-20">
            <ClipLoader size="30" sizeUnit="px"/>
        </div>
    );

    /** what about for smaller devices */
    
    const slides_to_show = event.profiles.map((profile,index) => {
            return (
                <>
                    <div className="d-block d-sm-none d-md-none d-lg-none">
                        <div className="element_mobile" key={index}>
                            <div className="element_content_slideshow" style={{ backgroundImage: `url(${profile})` }}>
                                <div className="element_content_slideshow_inner"></div>
                            </div>
                        </div>
                    </div>
                    <div className="d-none d-sm-block d-md-block d-lg-block">
                        <div className="element_desktop" key={index}>
                            <div className="element_content_slideshow" style={{ backgroundImage: `url(${profile})` }}>
                                <div className="element_content_slideshow_inner"></div>
                            </div>
                        </div>
                    </div>
                </>
            )
    });

    return (
        <>
        <GridContainer>
            <GridItem xs={12} md={12} sm={12}>
            <h4 className="card_heading">{event['name']}</h4>
            </GridItem>
            <GridItem xs={12} md={12} sm={12}>
                <div className="container">
                {
                    event.profiles.length > 1 ? (
                        <Slick {...slickSettings}>
                            {slides_to_show}
                        </Slick>
                    ) : (
                        <>
                            {/** for mobile */}
                            <div className="d-block d-sm-none d-md-none d-lg-none">
                                <div className="mobile_element_content_slide" style={{
                                    backgroundImage: `url(${event.profiles[0]})`
                                }}>
                                    <div className="element_content_slideshow_inner" />
                                </div>
                            </div>
                            {/** for desktop */}
                            <div className="d-none d-sm-block d-md-block d-lg-block">
                                <div className="desktop_element_content_slide" style={{
                                    backgroundImage: `url(${event.profiles[0]})`
                                }}>
                                    <div className="element_content_slideshow_inner" />
                                </div>
                            </div>
                        </>
                    )
                }
                <div className="eventDesc">
                    <h4 className="event_heading">Description</h4>
                    <p className="event_content">
                        {event.description}
                    </p>
                </div>
                <div className="eventTimeDate">
                    <h4 className="event_heading">Event's Time and Venue</h4>
                    <p className="event_content">
                        Event shall happen in the {event['time']} at {event['venue']}
                    </p>
                </div>
                <div className="organizerInfo">
                    <h4 className="event_heading">Organizer info</h4>
                    <div className="media">
                        <img src={
                                event['organizer']['profile'] ? event['organizer']['profile'] : avatar
                        } className="mr-3 organizer-media-img" alt={event['organizer']['firstname']} />                        
                        <div className="media-body">
                            <Link to={`/admin/user/${event['organizer']['_id']}`} className="user_name--event">
                                {
                                    event['organizer']['firstname'] + ' ' + event['organizer']['lastname']
                                }
                                </Link>
                        </div>                        
                    </div>
                </div>

                </div>
            </GridItem>
        </GridContainer>
        <GridContainer>
            <div className="container mt-20">
                {
                    related.length > 0 && <Carousel title={'Similar events'} card={'events'} data={related} />
                }
            </div>
        </GridContainer>
        </>
    )
}



export default withRouter(Event);