import {FEEDBACK_PENDING,FEEDBACK_SUCCESS} from './types';

const initialState = {
    pending:false,
    feedbacks:[]
};

export default function feedbackReducer(state=initialState,action){
    switch(action.type){
        case FEEDBACK_PENDING:
            return {
                ...state,
                pending:true
            }
        case FEEDBACK_SUCCESS:
            return {
                ...state,
                pending:false,
                feedbacks:action.payload
            }
        default:
            return {
                ...state
            }
    }
};

export const getFeedbackData = state => state.feedbacks;