import React from 'react';
import {Link,Redirect} from 'react-router-dom';
import {confirmAlert} from 'react-confirm-alert';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';

import {deleteHostel} from '../../store/hostels';

const HostelsCard = ({data:hostel,deleteHostel}) => {

    const del = async (_id) => {
        confirmAlert({
            title:'Confirm to delete',
            message:'Are you sure you want to delete the hostel',
            buttons:[
                {
                    label:'Yes',
                    onClick:async() => {
                        deleteHostel(_id);
                        return;
                    }
                },
                {
                    label:'No',
                    onClick:() => {
                        return <Redirect to="/admin/hostels" />
                    }
                }
            ]
        })
    };

    return (
        <div className="card hostels_card">
            <img src={`${hostel.profiles[0]}`} className="card-img-top hostels_card_img" alt={hostel.name} />
            <div className="card-body">
                <Link className="card_heading" to={`/admin/hostels/${hostel._id}`}>{hostel.name} hostels</Link>
                <p><span className={`badge badge-danger`}>{hostel.location}</span></p>
                <p className="text-muted card_text">kshs {hostel.rent} @month</p>
                <Link to={`/admin/hostels/${hostel._id}`} className="btn btn-sm btn-outline-success links">view more</Link>
                <Link to={{
                    pathname: '/admin/hostel_form',
                    state: { '_id': hostel._id }
                }} className="btn btn-sm btn-outline-success links">edit hostel</Link>
                <button className="btn btn-sm btn-outline-danger links" onClick={() => del(hostel._id)}>delete</button>
            </div>
        </div>
    );
} 

const dispatchStateToProps = dispatch => bindActionCreators({
      deleteHostel
},dispatch);

export default connect(null,dispatchStateToProps)(HostelsCard);