import React from 'react';
import {Link} from 'react-router-dom';
//core-components

import avatar from '../../assets/img/faces/marc.jpg';

const ProductCard = ({data:product}) => {
    return (
        <div className="card product_card">
            <img src={product.images[0]} className="card-img-top product_card--img" alt={product.name} />
            <div className="card-body" style={{ padding: "5px" }}>
                <h5 className="card-title">{product.name} <span className="badge badge-success">{product.category}</span></h5>

                <p className="card-text">Price: kshs {product.price}</p>

                <div className="media">
                    <img className="product_card_seller--profile mr-3" alt={product.seller.firstname} src={
                        product.seller.profile ? product.seller.profile : avatar
                    } />
                    <div className="media-body">
                        <Link className="product_card_links--seller" to={`/admin/user/${product.seller._id}`}>By {product.seller.firstname}</Link>
                        <p className="modal_text"> uploaded on {new Date(product.uploadedAt).toLocaleDateString()}</p>
                    </div>
                </div>
            </div>
        </div>
    )
};

export default ProductCard;